.search {
	flex-basis: 270px;
	margin-right: 50px;
	.scroll-to-fixed-fixed & {
		flex-basis: 330px;
	}
	.icon {
		display: block;
		&.icon-search {
			.svg-icon('icon-search', 16px, 16px);
		}
	}
	&__field {
		position: relative;
		&:after {
			content: '';
			display: block;
			.svg-icon('icon-search', 16px, 16px);
			position: absolute;
			right: 18px;
			top: 50%;
			margin-top: -8px;
		}
	}
	&__input {
		height: 30px;
		padding-left: 15px;
		padding-right: 45px;
		.font-size(13);
	}
	&_mobile {
		display: none;
		margin-right: 0;
		padding: 13px 20px;
		position: fixed;
		width: 100%;
		top: 0;
		z-index: 7;
		background: white;
		transition: transform .2s ease;
		transform: translateY(0);
		.search__input {
			height: 34px;
			padding-left: 20px;
			.font-size(14);
		}
	}
	&_opened {
		transform: translateY(100%);
	}
}

@media screen and (max-width: 980px) {
	.search {
		flex-grow: 20;
		flex-basis: auto;
		margin-right: 27px;
		.scroll-to-fixed-fixed & {
			margin-right: 20px;
			flex-basis: auto;
		}
	}
}

@media screen and (max-width: 768px) {
	.search {
		margin-right: 20px;
	}
}

@media screen and (max-width: 620px) {
	.search {
		display: none;
		&_mobile {
			display: block;
		}
	}
}