.tooltip {
	background: #FFFFFF;
	border: 1px solid #EFEFEF;
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.05);
	position: absolute;
	border-radius: 5px;
	z-index: 3;
	width: 240px;
	height: auto;
	position: absolute;
	top: -16px;
	left: 24px;
	display: none;
	opacity: 0;
	visibility: hidden;
	transform: translateX(15px);
	transition: opacity .2s ease, visibility .2s ease, transform .2s ease;
	&_active {
		animation: tooltipShow .2s ease;
		display: block;
		opacity: 1;
		visibility: visible;
		transform: translateX(0);
	}
	&:before {
		content: '';
		border-style: solid;
		border-width: 5px 6px 5px 0;
		border-color: transparent #FFFFFF transparent transparent;
		position: absolute;
		top: 20px;
		left: -6px;
	}
	&__inner {
		padding: 10px 15px;
	}
	&__title {
		.regular();
		.font-size(12);
		line-height: 1.4;
		color: #423D3D;
		margin-bottom: 5px;
	}
	&__text {
		.regular();
		.font-size(12);
		line-height: 1.4;
		color: #797979;
	}

	.icon {
		display: block;
		cursor: pointer;
		position: absolute;
		top: 2px;
		right: 2px;
		border-radius: 5px;
		&.icon-close {
			.svg-icon-param('icon-close', 8px, 8px, '[fill]: #C4C4C4');
			background-position: center center;
			width: 20px;
			height: 20px;
		}
	}
}

@keyframes tooltipShow {
	0% {
		display: none;
		opacity: 0;
		visibility: hidden;
		transform: translateX(15px);
	}
	1% {
		display: block;
		opacity: 0;
		visibility: hidden;
		transform: translateX(15px);
	}
	100% {
		opacity: 1;
		visibility: visible;
		transform: translateX(0);
	}
}

@media screen and (max-width: 768px) {
	.tooltip {
		top: 100%;
		left: 0;
		width: 100%;
		max-width: 70%;
		transform: translate3d(0,15px,0);
		&_active {
			transform: translate3d(0,0,0);
		}
		&:before {
			display: none;
		}
		&__inner {
			padding: 15px;
		}
		&__title {
			.font-size(14);
			margin-bottom: 5px;
		}
		&__text {
			.font-size(14);
			color: #797979;
		}

		.icon {
			display: block;
			cursor: pointer;
			position: absolute;
			top: 12px;
			right: 12px;
			&.icon-close {
				.svg-icon-param('icon-close', 14px, 14px, '[fill]: #C4C4C4');
				background-position: center center;
				width: 20px;
				height: 20px;
			}
		}
	}

	@keyframes tooltipShow {
		0% {
			display: none;
			opacity: 0;
			visibility: hidden;
			transform: translate3d(0,15px,0);
		}
		1% {
			display: block;
			opacity: 0;
			visibility: hidden;
			transform: translate3d(0,15px,0);
		}
		100% {
			opacity: 1;
			visibility: visible;
			transform: translate3d(0,0,0);
		}
	}
}


@media screen and (max-width: 400px) {
	.tooltip {
		max-width: none;
	}
}