.footer-nav {
	&__item {
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__link {
		text-decoration: none;
		color: #797979;
		&:hover {
			.footer-nav__link-name {
				text-decoration: none;
			}
		}
	}
	&__link-name {
		.regular();
		.font-size(14);
		line-height: 1.2;
		text-decoration: underline;
		color: #797979;
	}
	.icon {
		display: inline-block;
		vertical-align: middle;
		&.icon-forward {
			.svg-icon('icon-forward', 9px, 8px);
		}
	}
}
