.range-slider {
	padding-bottom: 7px;
	&__top {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 15px;
	}
	&__box {
		width: 50%;
		text-align: left;
		&:last-child {
			text-align: right;
		}
	}
	&__box-name {
		.regular();
		.font-size(13);
		line-height: 1.2;
		color: #797979;
		margin-right: 1px;
		display: inline-block;
		vertical-align: middle;
	}
	&__input-wrap {
		display: inline-block;
		vertical-align: middle;
		width: 70%;
	}
	&__input {
		appearance: none;
		border-radius: 0;
		border: 0;
		box-shadow: none;
		background: none;
		padding: 0;
		margin: 0;
		&::placeholder {
			color: #797979;
		}

		width: 100%;
		height: 30px;
		border-radius: 5px;
		background: white;
		border: 1px solid #E5E5E5;
		text-align: center;
		.regular();
		.font-size(13);
		line-height: 1.2;
		color: #797979;
	}
}

@media screen and (max-width: 768px) {
	.range-slider {
		padding-bottom: 10px;
		&__top {
			margin-bottom: 33px;
		}
		&__box-name {
			.font-size(14);
			margin-right: 6px;
		}
		&__input {
			height: 34px;
			.font-size(14);
		}
	}
}