.br-theme-css-stars {
	.br-widget {
		height: 28px;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		a {
			text-decoration: none;
			height: 24px;
			width: 26px;
			position: relative;
			background: url(../img/rating.svg) no-repeat;
			background-size: 32px;
			margin-right: 5px;
			&:after {
				content: '';
				background: url(../img/rating-fill.svg) no-repeat;
				background-size: 32px;
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				opacity: 0;
				visibility: hidden;
				transition: all .2s ease;
			}
			&.br-active {
				&:after {
					opacity: 1;
					visibility: visible;
				}
			}
			&.br-selected {
				&:after {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}
	.br-current-rating {
		margin-left: 10px;
		margin-top: 3px;
		.regular();
		.font-size(15);
		color: #797979;
	}
	.br-readonly {
		a {
			cursor: default;
		}
	}
}
@media print {
	.br-theme-css-stars .br-widget a:after {
		content: "\2606";
		color: black;
	}
	.br-theme-css-stars .br-widget a.br-active:after,
	.br-theme-css-stars .br-widget a.br-selected:after {
		content: "\2605";
		color: black;
	}
}
