.delivery {
	&__block {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__title-wrap {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		flex-wrap: wrap;
		margin-bottom: 10px;
	}
	&__title {
		.regular();
		.font-size(16);
		line-height: 1.2;
		color: #423D3D;
	}
	&__info {}
	&__row {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: stretch;
		min-height: 40px;
		&:nth-child(odd) {
			background: #F7F7F7;
		}
		&:nth-child(even) {
			background: white;
		}
	}
	&__col {
		.regular();
		.font-size(16);
		line-height: 1.2;
		color: #797979;
		&_address {
			flex-basis: 300px;
			.delivery__col-inner {
				padding-left: 20px;
			}
		}
		&_price {
			flex-basis: 160px;
		}
		&_date {
			flex-basis: 240px;
		}
		&_logos {
			flex-basis: 240px;
			.delivery__col-inner {
				padding-right: 20px;
				text-align: center;
				justify-content: center;
			}
		}
	}
	&__col-title {
		display: none;
		.regular();
		.font-size(16);
		line-height: 1.2;
		text-align: center;
		color: #423D3D;
		margin-bottom: 5px;
	}
	&__col-inner {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		height: 100%;
		padding: 6px 10px;
	}
	&__payment-logo {
		max-height: 28px;
		margin: 0 10px;
	}

	.icon {
		display: block;
		margin-right: 15px;
		flex-shrink: 0;
		&.icon-bags {
			.svg-icon('icon-bags', 50px, 50px);
		}
		&.icon-courier {
			.svg-icon('icon-courier', 50px, 50px);
		}
	}
}

@media screen and (max-width: 768px) {
	.delivery {
		&__block {
			margin-bottom: 0;
			padding: 25px 20px 5px;
			border-bottom: 1px solid #F7F7F7;
			&:first-child {
				padding-top: 0;
			}
			&:last-child {
				padding-bottom: 0;
				border-bottom: 0;
			}
		}
		&__title-wrap {
			margin-bottom: 20px;
		}
		&__info {
			display: flex;
			flex-wrap: wrap;
			flex-direction: row;
			justify-content: flex-start;
			align-items: stretch;
		}
		&__row {
			width: calc(~"(100% - 20px) / 2");
			flex-direction: column;
			justify-content: center;
			background: #FFFFFF;
			border: 1px solid #EFEFEF;
			box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.05);
			border-radius: 5px;
			margin-bottom: 20px;
			margin-right: 20px;
			padding: 20px;
			&:nth-child(odd) {
				background: none;
			}
			&:nth-child(even) {
				background: none;
			}
			&:nth-child(2n) {
				margin-right: 0;
			}
		}
		&__col {
			flex-basis: auto;
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 0;
			}
			&_address {
				.delivery__col-inner {
					padding-left: 0;
				}
			}
			&_logos {
				.delivery__col-inner {
					padding-right: 0;
				}
			}
		}
		&__col-title {
			display: block;
		}
		&__col-inner {
			justify-content: center;
			align-items: center;
			text-align: center;
			height: auto;
			padding: 0;
		}
	}
}

@media screen and (max-width: 620px) {
	.delivery {
		&__block {
			padding-bottom: 25px;
		}
		&__info {
			flex-direction: column;
		}
		&__row {
			width: 100%;
			margin-right: 0;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}

@media screen and (max-width: 400px) {
	.delivery {
		.icon {
			margin-right: 10px;
			&.icon-bags {
				.svg-icon('icon-bags', 40px, 40px);
			}
			&.icon-courier {
				.svg-icon('icon-courier', 40px, 40px);
			}
		}
	}
}