.popup {
	width: 480px;
	background: #FFFFFF;
	box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
	border-radius: 5px;
	margin: 0 auto;
	position: relative;
	padding: 30px 40px;
	&_big {
		width: 600px;
	}
	&_full {
		max-width: 940px;
		width: auto;
	}
	&_card {
		width: 500px;
	}

	&__close {
		appearance: none;
		padding: 0;
		margin: 0;
		box-shadow: none;
		border-radius: 0;
		border: 0;
		background: none;
		cursor: pointer;
		width: 20px;
		height: 20px;
		position: absolute;
		z-index: 1;
		right: 10px;
		top: 10px;
		&:hover {
			.icon {
				transform: rotate(90deg);
				&.icon-close {
					.svg-icon-param('icon-close', 12px, 12px, '[fill]: #1F9BD7');
				}
			}
		}
		.icon {
			display: block;
			margin: 0 auto;
			transition: transform .2s ease, background .2s ease;
			&.icon-close {
				.svg-icon-param('icon-close', 12px, 12px, '[fill]: #C4C4C4');
			}
		}
	}
	&__nav {
		appearance: none;
		padding: 0;
		margin: 0;
		box-shadow: none;
		border-radius: 0;
		border: 0;
		background: none;
		cursor: pointer;
		position: absolute;
		width: 55px;
		height: 100%;
		border-radius: 5px;
		background: rgba(0,0,0,.0);
		transition: background-color .2s ease;
		top: 0;
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: -12.5px;
			margin-left: -7.5px;
			transition: background .2s ease;
		}
		&:hover {
			background: rgba(0,0,0,.15);
		}
		&_prev {
			right: 100%;
			&:after {
				.svg-icon-param('icon-prev', 15px, 25px, '[fill]: #C4C4C4');
			}
			&:hover {
				&:after {
					.svg-icon-param('icon-prev', 15px, 25px, '[fill]: #1F9BD7');
				}
			}
		}
		&_next {
			left: 100%;
			&:after {
				.svg-icon-param('icon-next', 15px, 25px, '[fill]: #C4C4C4');
			}
			&:hover {
				&:after {
					.svg-icon-param('icon-next', 15px, 25px, '[fill]: #1F9BD7');
				}
			}
		}
	}
	&__title {
		margin-bottom: 20px;
		.regular();
		.font-size(18);
		line-height: 1.2;
		color: #423D3D;
	}
	&__img-wrap {
		text-align: center;
		margin-bottom: 20px;
	}
	&__certificate {
		max-width: 100%;
		vertical-align: middle;
	}
	&__caption {
		text-align: center;
		.regular();
		.font-size(15);
		line-height: 1.2;
		color: #423D3D;
	}
}

@media screen and (max-width: 980px) {
	.popup {
		&_full {
			max-width: 600px;
		}
	}
}

@media screen and (max-width: 620px) {
	.popup {
		&_full {
			max-width: none;
			width: 100%;
		}
		width: 100%;
		border-radius: 0;
		padding: 0;
		&__close {
			right: 17px;
			top: 20px;
			&:hover {
				.icon {
					&.icon-close {
						.svg-icon-param('icon-close', 14px, 14px, '[fill]: #1F9BD7');
					}
				}
			}
			.icon {
				&.icon-close {
					.svg-icon-param('icon-close', 14px, 14px, '[fill]: #797979');
				}
			}
		}
		&__title {
			margin-bottom: 0;
			.font-size(16);
			padding: 20px;
			background: #FBFBFB;
		}
		&__img-wrap {
			margin-bottom: 0;
			padding: 0 20px;
		}
		&__caption {
			padding: 20px 0;
		}
	}
}