@fonts: "../fonts/";

// Fira sans
@import url('https://fonts.googleapis.com/css?family=Fira+Sans:300,400,600&subset=cyrillic-ext');

// Days one
@font-face {
	font-family: 'Days';
	src: url('@{fonts}Days/Days.eot');
	src: url('@{fonts}Days/Days.woff2') format('woff2'),
		 url('@{fonts}Days/Days.woff') format('woff'),
		 url('@{fonts}Days/Days.ttf') format('truetype'),
		 url('@{fonts}Days/Days.svg#Days') format('svg'),
		 url('@{fonts}Days/Days.eot?#iefix') format('embedded-opentype');
	font-weight: normal;
	font-style: normal;
}

.light() {
	font-family: 'Fira Sans', sans-serif;
	font-weight: 300;
}
.regular() {
	font-family: 'Fira Sans', sans-serif;
	font-weight: 400;
}

.semi-bold() {
	font-family: 'Fira Sans', sans-serif;
	font-weight: 600;
}

.days() {
	font-family: 'Days', sans-serif;
	font-weight: 400;
}