.top-right-menu {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: flex-start;
	flex-basis: 370px;
	&__item {}
	&__link {
		display: block;
		padding: 7px 10px;
		text-decoration: none;
		&_darken {
			background: #EFEFEF;
		}
		&_sign-in {
			padding-right: 0;
			.top-right-menu__name {
				border-style: dashed;
			}
		}
		&:hover {
			.top-right-menu__name {
				border-bottom-color: transparent;
			}
		}
	}
	&__name {
		text-decoration: none;
		display: inline-block;
		vertical-align: middle;
		.regular();
		line-height: 1.1;
		.font-size(12);
		color: #797979;
		transition: border-color .2s ease;
		border-bottom: 1px solid #797979;
	}

	.icon {
		display: inline-block;
		vertical-align: middle;
		margin-right: 3px;
		&.icon-layers {
			.svg-icon('icon-layers', 16px, 16px);
		}
		&.icon-user {
			.svg-icon('icon-user', 16px, 16px);
		}
	}
}


@media screen and (max-width: 980px) {
	.top-right-menu {
		flex-basis: auto;
		&__item {
			margin-left: 10px;
			&:first-child {
				margin-left: 0;
			}
		}
	}
}

