.rating-stat {
	&__item {
		.regular();
		.font-size(16);
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		margin-bottom: 5px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__count {
		color: #423D3D;
		white-space: nowrap;
	}
	&__star {
		display: inline-block;
		background: url(../img/rating-fill.svg) no-repeat;
		background-size: 21px;
		width: 18px;
		height: 15px;
		margin-left: 8px;
	}
	&__chart {
		margin: 0 10px;
		width: 200px;
		height: 11px;
		background: #EFEFEF;
		border-radius: 20px;
	}
	&__chart-fill {
		height: 100%;
		background: #1F9BD7;
		border-radius: 20px;
	}
	&__reviews {
		color: #797979;
	}
	&__reviews-link {
		color: #63C2EF;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
}

@media screen and (max-width: 400px) {
	.rating-stat {
		&__chart {
			width: 120px;
		}
	}
}