.product-wrap {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	margin-bottom: 20px;
	&__col {
		&:first-child {
			max-width: 695px;
			flex-basis: 695px;
			margin-right: 20px;
		}
		&:last-child {
			max-width: 225px;
			align-self: stretch;
		}
	}
}

@media screen and (max-width: 980px) {
	.product-wrap {
		flex-direction: column;
		&__col {
			&:first-child {
				max-width: none;
				width: 100%;
				flex-basis: auto;
				margin-right: 0;
				margin-bottom: 20px;
			}
			&:last-child {
				max-width: none;
				width: 100%;
				align-self: flex-start;
			}
		}
	}
}

@media screen and (max-width: 620px) {
	.product-wrap {
		&__col {
			&:first-child {
				margin-bottom: 0;
			}
		}
	}
}