// 1: TABS
// 2: TABS MENU

// 1: TABS
.tabs {
	&_full {
		.tabs__content-wrap {
			padding: 40px 0 80px;
		}

		.tabs-menu {
			width: 100%;
		}
		.tabs-menu__item {
			flex: 1 1 auto;
		}
		.tabs-menu__link {
			padding: 12px 10px;
			min-width: 0;
		}
	}
	&__reviews-head {
		display: none;
		padding: 0 20px 25px;
		border-bottom: 1px solid #F7F7F7;
	}
	&__head {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: flex-end;
	}
	&__show-all {
		.regular();
		.font-size(15);
		line-height: normal;
		text-decoration: underline;
		color: #63C2EF;
		&:hover {
			text-decoration: none;
		}
	}
	&__content-wrap {
		padding-top: 50px;
	}
	&__content {
		display: none;
		&:first-child {
			display: block;
		}
	}
	&__section {
		margin-bottom: 40px;
		&:last-child {
			margin-bottom: 0;
		}
		&_analogs {
			.tabs__section-title {
				margin-bottom: 40px;
			}
		}
	}
	&__section-title {
		.regular();
		.font-size(18);
		line-height: 1.2;
		color: #423D3D;
		margin-bottom: 20px;
	}
	&__section-footer {
		padding-top: 5px;
		text-align: center;
		.btn {
			min-width: 250px;
		}
	}
	&__bottom {
		display: none;
		text-align: center;
		&_visible {
			display: block;
			padding-top: 5px;
		}
	}
}

// 2: TABS MENU
.tabs-menu {
	border-bottom: 1px solid #EFEFEF;
	display: inline-flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	flex-wrap: wrap;
	&__item {
		&_current {
			.tabs-menu__link {
				color: #1F9BD7;
				&:after {
					opacity: 1;
				}
				.icon {
					&.icon-logo {
						.svg-icon-param('icon-logo', 22px, 22px, '[fill]: #1F9BD7');
					}
					&.icon-ymarket {
						.svg-icon-param('icon-ymarket', 22px, 22px, '[fill]: #1F9BD7');
					}
					&.icon-vk2 {
						.svg-icon-param('icon-vk2', 22px, 22px, '[fill]: #1F9BD7');
					}
					&.icon-flamp {
						.svg-icon-param('icon-flamp', 22px, 22px, '[fill]: #1F9BD7');
					}
				}
			}
			.tabs-menu__count {
				color: white;
				background: #1F9BD7;
			}
		}
		&:hover {
			.tabs-menu__link {
				color: #1F9BD7;
				.icon {
					&.icon-logo {
						.svg-icon-param('icon-logo', 22px, 22px, '[fill]: #1F9BD7');
					}
					&.icon-ymarket {
						.svg-icon-param('icon-ymarket', 22px, 22px, '[fill]: #1F9BD7');
					}
					&.icon-vk2 {
						.svg-icon-param('icon-vk2', 22px, 22px, '[fill]: #1F9BD7');
					}
					&.icon-flamp {
						.svg-icon-param('icon-flamp', 22px, 22px, '[fill]: #1F9BD7');
					}
				}
			}
		}
	}
	&__link {
		position: relative;
		display: block;
		padding: 12px 20px;
		min-width: 180px;
		text-align: center;
		text-decoration: none;
		.regular();
		.font-size(15);
		line-height: normal;
		color: #797979;
		transition: color .2s ease;
		&:after {
			content: '';
			position: absolute;
			bottom: -1px;
			left: 0;
			width: 100%;
			height: 4px;
			background: #1F9BD7;
			opacity: 0;
			transition: opacity .2s ease;
		}

		.icon {
			display: inline-block;
			vertical-align: -5px;
			margin-right: 8px;
			&.icon-logo {
				.svg-icon-param('icon-logo', 22px, 22px, '[fill]: #797979');
			}
			&.icon-ymarket {
				.svg-icon-param('icon-ymarket', 22px, 22px, '[fill]: #797979');
			}
			&.icon-vk2 {
				.svg-icon-param('icon-vk2', 22px, 22px, '[fill]: #797979');
			}
			&.icon-flamp {
				.svg-icon-param('icon-flamp', 22px, 22px, '[fill]: #797979');
			}
		}
	}
	&__count {
		.regular();
		text-align: center;
		.font-size(12);
		color: #797979;
		background: #EFEFEF;
		border-radius: 10px;
		padding: 3px 7px;
		margin-left: 7px;
		transition: color .2s ease, background .2s ease;
		line-height: normal;
	}
}

@media screen and (max-width: 980px) {
	.tabs {
		&_reviews {
			.tabs-menu {
				width: 100%;
			}
			.tabs-menu__item {
				flex: 1 1 auto;
			}
			.tabs-menu__link {
				min-width: 0;
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.tabs {
		.container {
			padding: 0;
		}
		&__show-all {
			display: none;
		}
		&__bottom {
			display: block;
		}
		&_full {
			display: none;
		}
		&_reviews {
			.tabs__bottom_visible {
				padding-top: 25px;
			}
		}
	}

	.tabs-menu {
		width: 100%;
		&__item {
			flex: 1 1 auto;
		}
		&__link {
			min-width: 0;
		}
	}
}

@media screen and (max-width: 620px) {
	.tabs {
		&_reviews {
			.tabs__head {
				display: none;
			}
		}
		&__reviews-head {
			display: block
		}
	}


	.tabs-menu {
		&__link {
			.font-size(14);
			padding: 12px 5px;
		}
	}
}

@media screen and (max-width: 400px) {
	.tabs {
		&__content-wrap {
			padding-top: 25px;
		}
	}
}