.gallery-thumbs {
	position: relative;
	&_full {
		.gallery-thumbs__slides {
			max-width: 350px;
			margin: 0 auto;
		}
	}
	&__slides {
		margin: 0 20px;
	}
	&__slide {
		padding: 5px;
		&.slick-current {
			.gallery-thumbs__cover {
				border-color: #EFEFEF;
				box-shadow: 0px 0px 15px rgba(0,0,0,.05);
			}
		}
		&_video {
			.gallery-thumbs__cover {
				position: relative;
				&:after {
					content: '';
					width: 100%;
					height: 100%;
					background: rgba(0, 0, 0, 0.7);
					position: absolute;
					width: 100%;
					height: 100%;
					border-radius: 5px;
				}
				&:before {
					content: '';
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 7px 0 7px 12px;
					border-color: transparent transparent transparent white;
					position: absolute;
					top: 50%;
					left: 50%;
					margin-top: -7px;
					margin-left: -6px;
					z-index: 1;
					transform: scale(1);
					transition: transform .2s ease;
				}
			}
			&:hover {
				.gallery-thumbs__cover {
					&:before {
						transform: scale(1.32);
					}
				}
			}
		}
	}
	&__cover {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 60px;
		height: 60px;
		border: 1px solid white;
		border-radius: 5px;
		box-shadow: 0px 0px 15px rgba(0,0,0,0);
		background: white;
		transition: border-color .2s ease, box-shadow .2s ease;
		cursor: pointer;
		&:hover {
			border-color: #EFEFEF;
			box-shadow: 0px 0px 15px rgba(0,0,0,.05);
		}
	}
	&__image {
		max-width: 100%;
		max-height: 100%;
	}
	&__nav {
		appearance: none;
		padding: 0;
		border: 0;
		box-shadow: none;
		overflow: hidden;
		cursor: pointer;
		width: 20px;
		height: 60px;
		position: absolute;
		top: 50%;
		margin-top: -30px;
		background: white;
		&:after {
			content: '';
			position: absolute;
			display: block;
			top: 50%;
			left: 50%;
			margin-left: -3.5px;
			margin-top: -6px;
			transition: background .2s ease, left .2s ease;
		}
		&_prev {
			left: 0;
			&:after {
				.svg-icon-param('icon-arrow-left', 7px, 12px, '[fill]: #C4C4C4');
			}
			&:hover {
				&:after {
					.svg-icon-param('icon-arrow-left', 7px, 12px, '[fill]: #423d3d');
				}
			}
		}
		&_next {
			right: 0;
			&:after {
				.svg-icon-param('icon-arrow-right', 7px, 12px, '[fill]: #C4C4C4');
			}
			&:hover {
				&:after {
					.svg-icon-param('icon-arrow-right', 7px, 12px, '[fill]: #423d3d');
				}
			}
		}
	}
}