.news-block {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	flex-wrap: wrap;
	&__article {
		width: calc(~"(100% - 20px) / 2");
		background: #FFFFFF;
		border: 1px solid transparent;
		box-shadow: 0px 0px 15px transparent;
		border-radius: 5px;
		transition: border-color .2s ease, box-shadow .2s ease;
		cursor: pointer;
		&:hover {
			border-color: #EFEFEF;
			box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.05);
		}
		&:first-child {
			margin-right: 20px;
		}
	}
	&__image-wrap {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 230px;
	}
	&__image {
		max-height: 100%;
		max-width: 100%;
	}
	&__info {
		padding: 20px;
	}
	&__title-wrap {
		margin-bottom: 10px;
	}
	&__title {
		.regular();
		.font-size(24);
		line-height: 1.45;
		text-decoration: none;
		border-bottom: 1px solid #63C2EF;
		color: #63C2EF;
		transition: border-color .2s ease;
		&:hover {
			border-color: transparent;
		}
	}
	&__info-wrap {
		display: flex;
		border-fit: repeat;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
		.regular();
		.font-size(16);
		line-height: normal;
		margin-bottom: 10px;
	}
	&__date {
		color: #C4C4C4;
		margin-right: 6px;
	}
	&__time {
		color: #C4C4C4;
		margin-right: 25px;
	}
	&__comments {
		text-decoration: underline;
		color: #63C2EF;
		&:hover {
			text-decoration: none;
		}
	}
	&__text {
		.regular();
		.font-size(14);
		line-height: 1.4;
		color: #423D3D;
		height: 78px;
		overflow-y: hidden;
	}
}

@media screen and (max-width: 980px) {
	.news-block {
		&__title {
			.font-size(20);
		}
		&__info-wrap {
			.font-size(14);
		}
	}
}

@media screen and (max-width: 768px) {
	.news-block {
		display: none;

		&__article {
			width: 100%;
			margin-bottom: 0;
			&:first-child {
				margin-right: 0;
			}
			&:last-child {
				margin-bottom: 0;
			}
		}

		.news-slider & {
			display: block;
			max-width: 500px;
			margin: 0 auto;
		}
	}
}