.index-list {
	margin: 200px 0;
	&__item {
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__link {
		.regular();
		.font-size(20);
		line-height: 1.2;
		color: #63C2EF;
		text-decoration: none;
		border-bottom: 1px solid #63C2EF;
		transition: color .2s ease, border-color .2s ease;
		&:hover {
			color: darken(#63C2EF, 20%);
			border-color: white;
		}
		&:visited {
			color: #423d3d;
			border-color: white;
		}
	}
}

@media screen and (max-width: 768px) {
	.index-list {
		margin: 75px 0;
		&__item {
			margin-bottom: 15px;
			&:last-child {
				margin-bottom: 0;
			}
		}
		&__link {
			.regular();
			.font-size(20);
			line-height: 1.2;
			color: #63C2EF;
			text-decoration: none;
			border-bottom: 1px solid #63C2EF;
			transition: color .2s ease, border-color .2s ease;
			&:hover {
				color: darken(#63C2EF, 20%);
				border-color: white;
			}
			&:visited {
				color: #423d3d;
				border-color: white;
			}
		}
	}
}