body {
  min-width: 320px;
  color: #222;
  text-align: left;
  font-family: 'Fira Sans', sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.2;
  position: relative;
  opacity: 0;
  transition: opacity .3s ease;
}
body.loaded {
  opacity: 1;
}
.overlay {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 4;
  visibility: hidden;
  opacity: 0;
  transform: translateX(-100%);
}
.overlay_visible {
  animation: overlayShow 333ms ease both;
}
@keyframes overlayShow {
  0% {
    opacity: 0;
    transform: translateX(-100%);
    visibility: hidden;
  }
  1% {
    opacity: 0;
    transform: translateX(0);
    visibility: visible;
  }
  100% {
    opacity: 1;
    transform: translateX(0);
    visibility: visible;
  }
}
