.popup {
  width: 480px;
  background: #FFFFFF;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  margin: 0 auto;
  position: relative;
  padding: 30px 40px;
}
.popup_big {
  width: 600px;
}
.popup_full {
  max-width: 940px;
  width: auto;
}
.popup_card {
  width: 500px;
}
.popup__close {
  appearance: none;
  padding: 0;
  margin: 0;
  box-shadow: none;
  border-radius: 0;
  border: 0;
  background: none;
  cursor: pointer;
  width: 20px;
  height: 20px;
  position: absolute;
  z-index: 1;
  right: 10px;
  top: 10px;
}
.popup__close:hover .icon {
  transform: rotate(90deg);
}
.popup__close:hover .icon.icon-close {
  background: svg('icon-close', '[fill]: #1F9BD7') no-repeat 0 0;
  background-size: 12px 12px;
  width: 12px;
  height: 12px;
}
.popup__close .icon {
  display: block;
  margin: 0 auto;
  transition: transform .2s ease, background .2s ease;
}
.popup__close .icon.icon-close {
  background: svg('icon-close', '[fill]: #C4C4C4') no-repeat 0 0;
  background-size: 12px 12px;
  width: 12px;
  height: 12px;
}
.popup__nav {
  appearance: none;
  padding: 0;
  margin: 0;
  box-shadow: none;
  border-radius: 0;
  border: 0;
  background: none;
  cursor: pointer;
  position: absolute;
  width: 55px;
  height: 100%;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0);
  transition: background-color 0.2s ease;
  top: 0;
}
.popup__nav:after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -12.5px;
  margin-left: -7.5px;
  transition: background .2s ease;
}
.popup__nav:hover {
  background: rgba(0, 0, 0, 0.15);
}
.popup__nav_prev {
  right: 100%;
}
.popup__nav_prev:after {
  background: svg('icon-prev', '[fill]: #C4C4C4') no-repeat 0 0;
  background-size: 15px 25px;
  width: 15px;
  height: 25px;
}
.popup__nav_prev:hover:after {
  background: svg('icon-prev', '[fill]: #1F9BD7') no-repeat 0 0;
  background-size: 15px 25px;
  width: 15px;
  height: 25px;
}
.popup__nav_next {
  left: 100%;
}
.popup__nav_next:after {
  background: svg('icon-next', '[fill]: #C4C4C4') no-repeat 0 0;
  background-size: 15px 25px;
  width: 15px;
  height: 25px;
}
.popup__nav_next:hover:after {
  background: svg('icon-next', '[fill]: #1F9BD7') no-repeat 0 0;
  background-size: 15px 25px;
  width: 15px;
  height: 25px;
}
.popup__title {
  margin-bottom: 20px;
  font-family: 'Fira Sans', sans-serif;
