  padding: 0 20px;
  transition: background .2s ease;
  background: #1F9BD7;
  position: relative;
  overflow: hidden;
}
.category-menu__link:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  opacity: 0;
  z-index: 5;
  visibility: hidden;
  transform: translateX(-100%);
