.category-menu__link:after {
  content: '';
  width: 1px;
  height: 36px;
  background: #1FA5E5;
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -18px;
  opacity: 1;
  transition: opacity .2s ease;
