.tooltip {
  background: #FFFFFF;
  border: 1px solid #EFEFEF;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  z-index: 3;
  width: 240px;
  height: auto;
  position: absolute;
  top: -16px;
  left: 24px;
  display: none;
  opacity: 0;
  visibility: hidden;
  transform: translateX(15px);
  transition: opacity .2s ease, visibility .2s ease, transform .2s ease;
}
.tooltip_active {
  animation: tooltipShow .2s ease;
  display: block;
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}
.tooltip:before {
  content: '';
  border-style: solid;
  border-width: 5px 6px 5px 0;
  border-color: transparent #FFFFFF transparent transparent;
  position: absolute;
  top: 20px;
  left: -6px;
}
.tooltip__inner {
  padding: 10px 15px;
}
.tooltip__title {
  font-family: 'Fira Sans', sans-serif;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 1.4;
  color: #423D3D;
  margin-bottom: 5px;
}
.tooltip__text {
  font-family: 'Fira Sans', sans-serif;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 1.4;
  color: #797979;
}
.tooltip .icon {
  display: block;
  cursor: pointer;
  position: absolute;
  top: 2px;
  right: 2px;
  border-radius: 5px;
}
.tooltip .icon.icon-close {
  background: svg('icon-close', '[fill]: #C4C4C4') no-repeat 0 0;
  background-size: 8px 8px;
  width: 8px;
  height: 8px;
  background-position: center center;
  width: 20px;
  height: 20px;
}
@keyframes tooltipShow {
  0% {
    display: none;
    opacity: 0;
    visibility: hidden;
    transform: translateX(15px);
  }
  1% {
    display: block;
    opacity: 0;
    visibility: hidden;
    transform: translateX(15px);
  }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}
@media screen and (max-width: 768px) {
  .tooltip {
    top: 100%;
    left: 0;
    width: 100%;
    max-width: 70%;
    transform: translate3d(0, 15px, 0);
  }
  .tooltip_active {
    transform: translate3d(0, 0, 0);
  }
  .tooltip:before {
    display: none;
  }
  .tooltip__inner {
    padding: 15px;
  }
  .tooltip__title {
    font-size: 0.875rem;
    margin-bottom: 5px;
  }
  .tooltip__text {
    font-size: 0.875rem;
    color: #797979;
  }
  .tooltip .icon {
    display: block;
    cursor: pointer;
    position: absolute;
    top: 12px;
    right: 12px;
  }
  .tooltip .icon.icon-close {
    background: svg('icon-close', '[fill]: #C4C4C4') no-repeat 0 0;
    background-size: 14px 14px;
    width: 14px;
    height: 14px;
    background-position: center center;
    width: 20px;
    height: 20px;
  }
  @keyframes tooltipShow {
    0% {
      display: none;
      opacity: 0;
      visibility: hidden;
      transform: translate3d(0, 15px, 0);
    }
    1% {
      display: block;
      opacity: 0;
      visibility: hidden;
      transform: translate3d(0, 15px, 0);
    }
    100% {
      opacity: 1;
      visibility: visible;
      transform: translate3d(0, 0, 0);
    }
  }
}
