.reviews-list {
	margin-bottom: 20px;
	&__section {
		background: #FFFFFF;
		border: 1px solid white;
		box-shadow: 0px 0px 15px rgba(0, 0, 0, 0);
		border-radius: 5px;
		transition: border-color .2s ease, box-shadow .2s ease;
		margin-bottom: 20px;
		&:hover {
			border-color: #EFEFEF;
			box-shadow: 0px 0px 15px rgba(0, 0, 0, .05);
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__question {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		background: #FFFFFF;
		padding: 20px;
	}
	&__answer {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		background: #FBFBFB;
		padding: 20px;
		border-radius: 0 0 5px 5px;
		.reviews-list__author {
			margin-bottom: 10px;
		}
		.reviews-list__author-name {
			color: #423D3D;
		}
		.reviews-list__col {
			padding-left: 220px;
		}
	}
	&__col {
		&_stars {
			flex-basis: 220px;
			flex-shrink: 0;
		}
	}
	&__author {
		margin-bottom: 23px;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: flex-start;
	}
	&__author-holder {}
	&__author-name {
		.regular();
		.font-size(16);
		line-height: 1.2;
		color: #797979;
	}
	&__content {}
	&__descr {
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 20px;
		}
	}
	&__title-descr {
		.regular();
		.font-size(16);
		line-height: 1.2;
		color: #423D3D;
		margin-bottom: 10px;
	}
	&__bottom {
		.regular();
		.font-size(14);
		line-height: 1.2;
		color: #C4C4C4;
	}
	&__date {}
	&__city {}

	.icon {
		display: inline-block;
		&.icon-user {
			vertical-align: -2px;
			margin-right: 4px;
			.svg-icon('icon-user', 18px, 18px);
		}
	}
}

@media screen and (max-width: 768px) {
	.reviews-list {
		margin-bottom: 0;
		&__section {
			border-width: 0 0 1px 0;
			border-style: solid;
			border-color: #EFEFEF;
			box-shadow: none;
			border-radius: 0;
			margin-bottom: 0;
			&:hover {
				border: 0;
				box-shadow: none;
			}
		}
		&__col {
			&_stars {
				display: none;
			}
		}
		&__author {
			justify-content: flex-start;
			align-items: center;
			.vote-menu {
				display: none;
			}
		}
		&__author-name {
			vertical-align: middle;
		}
		&__content {
			.vote-menu {
				display: flex;
			}
		}
		&__bottom {
			margin-bottom: 20px;
		}
		&__answer {
			.reviews-list__col {
				padding-left: 0;
			}
			.reviews-list__bottom {
				margin-bottom: 0;
			}
		}
		.icon {
			&.icon-user {
				vertical-align: middle;
				.svg-icon('icon-user', 20px, 20px);
			}
		}
	}
}