.price-box {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	margin-bottom: 18px;
	&_hidden {
		display: none;
	}
	&__price {
		.semi-bold();
		line-height: 1.2;
		.font-size(20);
		color: #423D3D;
		margin: 0 4px;
	}
	&__old-price {
		.regular();
		line-height: 1.2;
		.font-size(16);
		color: #C4C4C4;
		margin: 0 4px;
		text-decoration: line-through;
	}
}

@media screen and (max-width: 768px) {
	.price-box {
		justify-content: flex-start;
		&__price {
			margin-left: 0;
		}
	}
}