.product-gallery {
	position: relative;
	&_in-popup {
		.product-gallery__label {
			top: 0;
			right: 60px;
		}
	}
	&__label {
		z-index: 3;
		position: absolute;
		top: -10px;
		right: -15px;
		background: #F32D2D;
		border-radius: 5px;
		text-align: center;
		height: 34px;
		line-height: 34px;
		.regular();
		.font-size(18);
		color: white;
		width: 70px;
		user-select: none;
		pointer-events: none;
	}
}