// 2: CATALOG MENU

// 1: CATALOG MENU WRAP
.category-menu-wrap {
	background: #1F9BD7;
}

// 2: CATALOG MENU
.category-menu {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	position: relative;
	z-index: 5;
	&:hover {
		.category-menu__item {
			&:hover {
				.category-menu__link {
					background: #198EC6;
				}
			}
			&:not(:hover) {
				.category-menu__link {
					&:before {
						animation: hideMenu 333ms ease both;
					}
					&:after {
						opacity: 0;
					}
				}
			}
		}
	}
	&__item {
		flex: 1;
		position: relative;
		&:nth-child(2) {
			.category-sub-menu {
				left: -100%;
			}
		}
		&:nth-child(3) {
			.category-sub-menu {
				left: -200%;
			}
		}
		&:nth-child(4) {
			.category-sub-menu {
				left: -300%;
			}
		}
		&:last-child {
			.category-menu__link {
				&:after {
					opacity: 0;
				}
			}
		}
	}
	&__link {
		text-align: center;
		.regular();
		.font-size(15);
		text-decoration: none;
		color: #FFFFFF;
		display: block;
		height: 50px;
		line-height: 48px;
		padding: 0 20px;
		position: relative;
		transition: background .2s ease;
		background: #1F9BD7;
		position: relative;
		overflow: hidden;
		&:before {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background: rgba(0,0,0,.7);
			opacity: 0;
			z-index: 5;
			visibility: hidden;
			transform: translateX(-100%);
		}
		&:after {
			content: '';
			width: 1px;
			height: 36px;
			background: #1FA5E5;
			position: absolute;
			right: 0;
			top: 50%;
			margin-top: -18px;
			opacity: 1;
			transition: opacity .2s ease;
		}
		&:hover {
			background: #198EC6;
			&:after {
				opacity: 0;
			}
		}
		.icon {
			display: inline-block;
			vertical-align: middle;
			margin-right: 7px;
			&.icon-lamps {
				.svg-icon-param('icon-lamps', 22px, 22px, '[fill]: #ffffff');
			}
			&.icon-xenon {
				.svg-icon-param('icon-xenon', 22px, 22px, '[fill]: #ffffff');
			}
			&.icon-led {
				.svg-icon-param('icon-led', 22px, 22px, '[fill]: #ffffff');
			}
			&.icon-lights {
				.svg-icon-param('icon-lights', 22px, 22px, '[fill]: #ffffff');
			}
			&.icon-expand {
				margin: 0 0 0 4px;
				.svg-icon-param('icon-expand', 8px, 5px, '[fill]: #ffffff');
			}
		}
	}
}

@keyframes hideMenu {
	0% {
		opacity: 0;
		transform: translateX(-100%);
		visibility: hidden;
	}
	1% {
		opacity: 0;
		transform: translateX(0);
		visibility: visible;
	}
	100% {
		opacity: 1;
		transform: translateX(0);
		visibility: visible;
	}
}

@media screen and (max-width: 980px) {
	.category-menu {
		&__link {
			.font-size(14);
			padding: 0 10px;
		}
	}
}

@media screen and (max-width: 768px) {
	.category-menu {
		display: none;
	}
}