.sub-menu {
	&_col-2 {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		.sub-menu__col {
			max-width: 50%;
			padding-right: 15px;
			min-width: 90px;
		}
	}
	&_fixed {
		width: 100%;
		.sub-menu__item {
			margin-bottom: 0;
		}
		.sub-menu__link {
			padding: 10px 20px;
			display: block;
		}
		.sub-menu__count {
			background: #E5E5E5;
		}
	}
	&__col {}
	&__list {
		list-style-type: none;
		list-style-image: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7);
	}
	&__item {
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__link {
		color: #63C2EF;
		text-decoration: none;
		&:hover {
			.sub-menu__link-name {
				text-decoration: none;
			}
		}
	}
	&__link-name {
		.regular();
		.font-size(15);
		line-height: 1.2;
		text-decoration: underline;
	}
	&__count {
		.regular();
		.font-size(11);
		line-height: 1.2;
		color: #797979;
		display: inline-block;
		padding: 2px 7px;
		background: #F7F7F7;
		border-radius: 20px;
		margin: 2px;
	}
}