.ui-slider {
	border-radius: 20px;
	border: 0;
	background: #EFEFEF;
	height: 4px;
	.ui-slider-range {
		background: #1F9BD7;
		border: 0;
	}
	.ui-slider-handle {
		width: 15px;
		height: 15px;
		border-radius: 50%;
		background: #FFFFFF;
		border: 2px solid #1F9BD7;
		top: 50%;
		margin-left: 15px / -2px;
		margin-top: 15px / -2;
	}
}

@media screen and (max-width: 768px) {
	.ui-slider {
		.ui-slider-handle {
			width: 20px;
			height: 20px;
			margin-left: -10px;
			margin-top: -10px;
		}
	}
}