.product-set {
	padding: 20px 0;
	margin-bottom: 20px;
	&_slider {
		.container {
			padding: 0;
		}
		.product-set__title {
			padding: 0 20px;
		}
		.product-set__item {
			&:after {
				display: none;
			}
		}
	}
	&__card {
		padding: 15px;
		min-width: 0;
		.product-card__body {
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			align-items: flex-start
		}
		.product-card__body-col {
			&:first-child {
				min-width: 50px;
			}
			&:last-child {
				padding-left: 10px;
			}
		}
		.product-card__label {
			display: none;
		}
		.product-card__image-wrap {
			height: 50px;
		}
		.product-card__tool {
			display: none;
		}
		.product-card__name-wrap {
			padding: 0;
			margin-bottom: 5px;
			text-align: left;
		}
		.product-card__name {
			.font-size(12);
			min-height: 34px;
			height: 34px;
		}
		.product-card__info-wrap {
			display: none;
		}
		.product-card__footer {
			display: none;
		}
		.price-box {
			flex-direction: column;
			justify-content: flex-start;
			align-items: flex-start;
			margin-bottom: 5px;
			min-height: 37px;
		}
		.price-box__price {
			.font-size(18);
			margin: 0;
		}
		.price-box__old-price {
			.font-size(14);
			margin: 0;
		}
		.product-card__available_hidden {
			max-width: 110px;
		}
		.available {
			.font-size(11);
			margin-bottom: 0;
		}
		.available__name {
			margin-bottom: 0;
		}
		.available__link {
			display: none;
		}
	}
	&__card-slide {
		.product-card__footer {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			.btn {
				.font-size(13);
				padding: 7px 10px;
				width: 110px;
			}
		}
		.product-card__button-wrap {
			margin-bottom: 0;
		}
		.features-tools__btn {
			width: 26px;
			height: 26px;
			.icon {
				&.icon-compare {
					.svg-icon('icon-add-compare', 22px, 22px);
				}
				&.icon-heart {
					.svg-icon-param('icon-heart', 16px, 16px, '[fill]: #C4C4C4');
				}
			}
			&:hover {
				.icon {
					&.icon-compare {
						.svg-icon-param('icon-add-compare', 22px, 22px, '[fill]: #1F9BD7');
					}
					&.icon-heart {
						.svg-icon-param('icon-heart', 16px, 16px, '[fill]: #F32D2D');
					}
				}
			}
			&_active {
				.icon {
					&.icon-compare {
						.svg-icon-param('icon-add-compare', 22px, 22px, '[fill]: #1F9BD7');
					}
					&.icon-heart {
						.svg-icon-param('icon-heart', 16px, 16px, '[fill]: #F32D2D');
					}
				}
			}
		}
	}
	&__title {
		display: inline-block;
		width: auto;
		.days();
		.font-size(24);
		line-height: 1.2;
		color: #423D3D;
		user-select: none;
		margin-bottom: 0;
		cursor: pointer;
		transition: margin-bottom .2s ease;
		.icon {
			display: inline-block;
			margin-left: 4px;
			vertical-align: middle;
			transition: transform .2s ease;
			transform: rotate(-90deg);
			&.icon-expand {
				.svg-icon-param('icon-expand', 16px, 10px, '[fill]: #423D3D');
			}
		}
		&:after {
			content: '';
			display: none;
			position: absolute;
			top: 50%;
			margin-top: -6px;
			right: 20px;
			transform: rotate(-90deg);
			transition: transform .2s ease;
			.svg-icon('icon-collapse', 12px, 12px);
		}
		&_active {
			margin-bottom: 25px;
			.icon {
				transform: rotate(0);
			}
		}
	}
	&__title-link {
		.regular();
		.font-size(15);
		text-decoration: underline;
		color: #63C2EF;
		line-height: 1.2;
		&:hover {
			text-decoration: none;
		}
	}
	&__content {}
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		justify-content: space-between;
		align-items: flex-start;
		margin-bottom: 20px;
	}
	&__item {
		max-width: 220px;
		position: relative;
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			margin-top: -6px;
			right: -16px;
			z-index: 2;
			.svg-icon('icon-plus', 12px, 12px);
		}
		&:last-child {
			&:after {
				display: none;
			}
		}
	}
	&__footer {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: flex-end;
	}
	&__footer-col {
		.btn {
			&:first-child {
				margin-right: 15px;
			}
		}
	}
	&__price-info {
		text-align: right;
		.regular();
		.font-size(16);
		line-height: 1.2;
		color: #C4C4C4;
		margin-bottom: 15px;
		&_sale {
			.font-size(18);
			color: #423D3D;
			.product-set__text {
				.font-size(18);
			}
			.product-set__price {
				.semi-bold();
				.font-size(24);
			}
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__text {}
	&__price {
		.font-size(20);
	}
}


@media screen and (max-width: 980px) {
	.product-set {
		margin-bottom: 0;
		&_slider {
			.product-set__item {
				width: auto;
				max-width: 365px;
			}
			.product-set__card-mobile {
				.product-card__footer {
					padding-top: 10px;
					display: flex;
				}
				.product-card__button-wrap {
					order: 2;
					width: 55%;
					padding-left: 10px;
					.btn {
						width: 100%;
					}
				}
				.product-card__features-tools {
					order: 1;
					width: 45%;
				}
				.features-tools__btn {
					width: 40px;
					height: 30px;
					.icon {
						&.icon-compare {
							.svg-icon('icon-add-compare', 27px, 27px);
						}
						&.icon-heart {
							.svg-icon-param('icon-heart', 20px, 20px, '[fill]: #C4C4C4');
						}
					}
					&:hover {
						.icon {
							&.icon-compare {
								.svg-icon-param('icon-add-compare', 27px, 27px, '[fill]: #1F9BD7');
							}
							&.icon-heart {
								.svg-icon-param('icon-heart', 20px, 20px, '[fill]: #F32D2D');
							}
						}
					}
					&_active {
						.icon {
							&.icon-compare {
								.svg-icon-param('icon-add-compare', 27px, 27px, '[fill]: #1F9BD7');
							}
							&.icon-heart {
								.svg-icon-param('icon-heart', 20px, 20px, '[fill]: #F32D2D');
							}
						}
					}
				}
			}
		}
		&__item {
			max-width: none;
			width: calc(~"(100% - 20px) / 2");
			margin-right: 20px;
			margin-bottom: 20px;
			&:nth-child(2n) {
				margin-right: 0;
			}
			&:after {
				display: none;
			}
		}
		&__card-mobile {
			.product-card__body-col {
				&:first-child {
					width: 45%;
				}
				&:last-child {
					width: 55%;
				}
			}
			.product-card__image-wrap {
				height: 150px;
			}
			.product-card__name-wrap {
				margin-bottom: 10px;
			}
			.product-card__name {
				.font-size(13);
				min-height: 34px;
				height: 34px;
			}
			.product-card__info-wrap {
				display: flex;
				margin-bottom: 10px;
			}
			.product-card__footer {
				display: none;
			}
			.price-box {
				flex-direction: row;
				flex-wrap: wrap;
				align-items: center;
				margin-bottom: 0;
				min-height: 0;
			}
			.price-box__price {
				.font-size(20);
				margin: 0 8px 0 0;
			}
			.price-box__old-price {
				.font-size(16);
				margin: 3px 0 0 0;
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.product-set {
		margin-bottom: 0;
		padding: 0;
		&_slider {
			display: none;
		}
		&__title {
			padding: 0 20px;
			margin-bottom: 0;
			background: #FBFBFB;
			height: 60px;
			line-height: 60px;
			position: relative;
			width: 100%;
			cursor: auto;
			.regular();
			.font-size(16);
			color: #797979;
			&:after {
				display: block;
			}
			&:before {
				content: '';
				width: 100%;
				height: 1px;
				background: #F3F3F3;
				position: absolute;
				left: 0;
				bottom: 0;
				opacity: 1;
				visibility: visible;
				transition: visibility .2s ease, opacity .2s ease;
			}
			.icon {
				display: none;
			}
			&_active {
				&:after {
					transform: rotate(0);
				}
				&:before {
					opacity: 0;
					visibility: hidden;
				}
			}
		}
		&__content {
			display: none;
			padding-bottom: 50px;
		}
		&__item {
			margin: 0;
			width: 50%;
		}
		&__card-mobile {
			padding: 25px 20px;
			.product-card__body-col {
				&:first-child {
					padding-right: 0;
				}
			}
			.product-card__characteristics {
				display: none;
			}
			.product-card__available {
				display: none;
			}
		}
		&__footer {
			flex-direction: column;
			justify-content: flex-start;
			align-items: flex-start;
			padding: 0 20px;
		}
		&__footer-col {
			width: 100%;
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: flex-start;
			align-items: center;
			&:first-child {
				order: 2;
			}
			&:last-child {
				order: 1;
				margin-bottom: 20px;
			}
			.btn {
				min-width: 155px;
				&:first-child {
					margin-right: 0;
					order: 2;
				}
				order: 1;
				margin-right: 20px;
			}
		}
		&__price-info {
			text-align: left;
			margin-bottom: 0;
			order: 2;
			&_sale {
				order: 1;
				margin-right: 10px;
				.product-set__price {
					.font-size(30);
					text-decoration: none;
				}
			}
		}
		&__text {
			display: none;
		}
		&__price {
			.font-size(26);
			text-decoration: line-through;
		}
	}
}

@media screen and (max-width: 620px) {
	.product-set {
		&__item {
			width: 100%;
			border-bottom: 1px solid #F7F7F7;
			&:after {
				display: block;
				top: auto;
				margin-top: auto;
				right: auto;
				left: 20px;
				width: 34px;
				height: 34px;
				background-position: 50% 50%;
				bottom: -17px;
				border-radius: 50%;
				border: 1px solid #F7F7F7;
				background-color: white;
			}
			&:last-child {
				border-bottom: 0;
			}
		}
		&__card-mobile {
			.product-card__body-col {
				&:first-child {
					width: auto;
					min-width: 150px;
				}
				&:last-child {
					width: auto;
				}
			}
		}
	}
}

@media screen and (max-width: 400px) {
	.product-set {
		&__footer-col {
			&:first-child {
				flex-direction: column;
			}
			.btn {
				min-width: 0;
				width: 100%;
				&:first-child {
					margin-bottom: 0;
				}
				margin: 0 0 20px;
			}
		}
	}
}