.sub-nav {
	position: absolute;
	top: 100%;
	width: 500%;
	left: -400%;
	display: none;
	&__inner-wrap {}
	&__list {
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		align-items: flex-start;
		list-style-type: none;
		list-style-image: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7);
	}
	&__item {
		flex-basis: auto;
		flex-grow: 1;
		&:first-child {
			.sub-nav__link {
				&:after {
					display: none;
				}
			}
		}
	}
	&__link {
		.regular();
		.font-size(15);
		color: #423D3D;
		text-align: center;
		display: block;
		background: white;
		text-decoration: none;
		transition: background .2s ease;
		height: 50px;
		line-height: 48px;
		padding: 0 18px;
		position: relative;
		&:hover {
			background: #EFEFEF;
			&:after {
				opacity: 0;
			}
		}
		&:after {
			content: '';
			width: 1px;
			height: 36px;
			position: absolute;
			background: #F2F2F2;
			left: -1px;
			top: 50%;
			margin-top: -18px;
			pointer-events: none;
		}
	}
}

@media screen and (max-width: 980px) {
	.sub-nav {
		&__list {
			height: 50px;
			align-items: stretch;
		}
		&__link {
			.regular();
			.font-size(14);
			color: #423D3D;
			height: 100%;
			line-height: 1.2;
			padding: 0 5px;
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}
}