.sort {
	margin-bottom: 10px;
	&__desktop {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
	}
	&__mobile {
		display: none;
	}
	&__title {
		.regular();
		.font-size(15);
		line-height: 1.2;
		color: #423D3D;
		margin-right: 16px;
	}
	&__items {
		flex-grow: 20;
	}
	&__link {
		.regular();
		.font-size(15);
		line-height: 1.2;
		color: #63C2EF;
		text-decoration: underline;
		margin-right: 16px;
		&_active {
			color: #423D3D;
			text-decoration: none;
		}
		&:hover {
			text-decoration: none;
		}
		&:last-child {
			margin-right: 0;
		}
	}
}

@media screen and (max-width: 980px) {
	.sort {
		&__title {
			.regular();
			.font-size(15);
			line-height: 1.2;
			color: #423D3D;
			margin-right: 8px;
		}
		&__items {
			flex-grow: 0;
			width: 100%;
			margin: 10px 0;
		}
	}
}

@media screen and (max-width: 768px) {
	.sort {
		margin: 15px 0;
		&__desktop {
			display: none;
		}
		&__mobile {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			.form-control__field_select-cover {
				width: 190px;
				margin: 10px;
			}
			.btn_filter {
				margin: 10px;
				min-width: 120px;
			}
		}
	}
}

@media screen and (max-width: 400px) {
	.sort {
		&__mobile {
			padding: 0 20px;
			.form-control__field_select-cover {
				width: 100%;
				margin: 10px 0;
			}
			.btn_filter {
				min-width: 0;
				width: 100%;
				margin: 10px 0;
			}
		}
	}
}