// 1: MAIN SLIDER
// 3: CARD SLIDER
// 6: FORGOTTEN SLIDER
// 7: SET SLIDER

// 1: MAIN SLIDER
.slick-slider.main-slider {
	.slick-list, .slick-track {
		height: 100%;
	}
	.slick-slide {
		img {
			display: inline;
		}
	}
	.slick-dots {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		position: absolute;
		width: 100%;
		bottom: 20px;
		pointer-events: none;
		li {
			&.slick-active {
				button {
					background: #1F9BD7;
				}
			}
			button {
				pointer-events: auto;
				display: block;
				border: 0;
				border-radius: 50%;
				margin: 0 4px;
				width: 13px;
				height: 13px;
				background: rgba(0, 0, 0, 0.2);
				color: transparent;
				cursor: pointer;
				transition: background .2s ease;
				outline: none;
				&:hover {
					background: lighten(#1F9BD7, 10%);
				}
			}
		}
	}
}

// 3: CARD SLIDER
.slick-slider.card-slider,
.slick-slider.news-slider {
	.slick-dots {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		margin: 25px 0;
		li {
			&.slick-active {
				button {
					background: #1F9BD7;
				}
			}
			button {
				appearance: none;
				padding: 0;
				pointer-events: auto;
				display: block;
				border: 0;
				border-radius: 50%;
				margin: 0 3px;
				width: 10px;
				height: 10px;
				background: rgba(0, 0, 0, 0.2);
				color: transparent;
				cursor: pointer;
				transition: background .2s ease;
				outline: none;
				&:hover {
					background: lighten(#1F9BD7, 10%);
				}
			}
		}
	}
}

// 6: FORGOTTEN SLIDER
.slick-slider.js-forgotten-slider {
	.slick-list {
		padding: 0 20px;
	}
	.slick-track {
		padding: 10px 0;
	}
	.slick-slide {
		margin: 0 10px;
	}
	.slick-arrow {
		border: 0;
		box-shadow: none;
		overflow: hidden;
		text-indent: -9999px;
		color: transparent;
		cursor: pointer;
		position: absolute;
		top: 50%;
		margin-top: -14px;
		z-index: 2;
		&.slick-prev {
			.svg-icon('icon-prev', 14px, 28px);
			left: 0;
		}
		&.slick-next {
			.svg-icon('icon-next', 14px, 28px);
			right: 0;
		}
	}
}

// SET SLIDER
.slick-slider.set__slider {
	display: block;
	border-bottom: 1px solid #F7F7F7;
	.slick-slide {
		width: 100%;
		border-bottom: 0;
	}
	.slick-dots {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		margin: 10px 0 25px;
		li {
			margin: 0 3px;
			display: block;
			&.slick-active {
				button {
					background: #1F9BD7;
				}
			}
			button {
				appearance: none;
				padding: 0;
				display: block;
				border: 0;
				border-radius: 50%;
				width: 10px;
				height: 10px;
				background: rgba(0, 0, 0, 0.2);
				color: transparent;
				cursor: pointer;
				transition: background .2s ease;
				outline: none;
				&:hover {
					background: lighten(#1F9BD7, 10%);
				}
			}
		}
	}
}