.btn {
	border: 1px solid #1F9BD7;
	display: inline-block;
	background: #1F9BD7;
	border-radius: 60px;
	.regular();
	.font-size(15);
	line-height: normal;
	text-align: center;
	color: white;
	padding: 9px 20px;
	min-width: 180px;
	text-decoration: none;
	cursor: pointer;
	transition-property: border-color, background, color;
	transition-duration: .2s;
	transition-timing-function: ease;
	&__text {
		.regular();
		.font-size(15);
		line-height: normal;
		color: white;
		display: inline-block;
		text-decoration: none;
	}
	&:hover {
		border-color: lighten(#1F9BD7, 10%);
		background: lighten(#1F9BD7, 10%);
	}

	&_icon {
		padding: 9px 43px;
		border-radius: 20px;
		.icon {
			display: inline-block;
			vertical-align: middle;
			&.icon-arrow {
				.svg-icon-param('icon-arrow', 8px, 8px, '[fill]: #ffffff');
			}
			&.icon-arrow-down {
				.svg-icon-param('icon-arrow-down', 8px, 8px, '[fill]: #ffffff');
			}
			&.icon-filter {
				.svg-icon('icon-filter', 12px, 12px);
			}
		}
	}
	&_gray {
		background: #EFEFEF;
		border-color: #EFEFEF;
		color: #797979;
		.btn__text {
			color: #797979;
		}
		.icon {
			&.icon-arrow {
				.svg-icon-param('icon-arrow', 8px, 8px, '[fill]: #797979');
			}
			&.icon-arrow-down {
				.svg-icon-param('icon-arrow-down', 8px, 8px, '[fill]: #797979');
			}
		}
		&:hover {
			background: darken(#EFEFEF, 2%);
			border-color: darken(#EFEFEF, 2%);
		}
	}
	&_dark {
		min-width: 0;
		padding: 10px 16px;
		background: #423D3D;
		border-color: #423D3D;
		&:hover {
			border-color: lighten(#423D3D, 10%);
			background: lighten(#423D3D, 10%);
		}
	}
	&_selected {
		background: white;
		color: #1F9BD7;
		border-color: #1F9BD7;
		&:hover {
			background: white;
			color: #1F9BD7;
		}
	}
}

@media screen and (max-width: 768px) {
	.btn {
		padding: 8px 15px;
		.font-size(14);
		&__text {
			.font-size(14);
		}
	}
}