.top-nav {
	width: calc(~"100% - 370px");
	&__list {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
	}
	&__item {
		position: relative;
		padding: 7px 0;
		margin-right: 30px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__link {
		display: block;
		text-decoration: none;
		&:hover {
			.top-nav__link-name {
				text-decoration: none;
			}
		}
	}
	&__link-name {
		display: inline-block;
		vertical-align: middle;
		text-decoration: underline;
		.regular();
		line-height: 1.1;
		.font-size(12);
		color: #797979;
	}

	.icon {
		display: inline-block;
		vertical-align: middle;
		&.icon-expand {
			.svg-icon('icon-expand', 8px, 5px);
		}
	}
}

@media screen and (max-width: 980px) {
	.top-nav {
		width: calc(~"100% - 350px");
		&__item {
			margin-right: 20px;
		}
	}
}