.jq-checkbox {
	width: 18px;
	height: 18px;
	background: #FFFFFF;
	border: 1px solid #C4C4C4;
	border-radius: 5px;
	box-shadow: none;
	transition: border-color .2s ease;
	margin: 0;
	&__div {
		position: absolute;
		margin: 0;
		top: 0;
		left: 0;
		opacity: 0;
		visibility: hidden;
		transition: opacity .2s ease, visibility .2s ease;
		border: 0;
		transform: rotate(0);
		top: 50%;
		margin-top: -4px;
		left: 50%;
		margin-left: -5px;
		.svg-icon('icon-check', 10px, 8px);
	}
	&.checked {
		border-color: #1F9BD7;
		&.focused {
			border-color: #1F9BD7;
		}
		.jq-checkbox__div {
			opacity: 1;
			visibility: visible;
			width: 10px;
			height: 8px;
			transform: rotate(0);
			border: 0;
			margin: -4px auto auto -5px;
		}
	}
	&.focused {
		border-color: #C4C4C4;
	}
}

.jq-selectbox {
	width: 100%;
	z-index: 3;
	&.focused {
		.jq-selectbox__select {
			border-color: #1F9BD7;
		}
	}
	&.dropup {
		.jq-selectbox__dropdown {
			padding-top: 16px;
			padding-bottom: 52px;
			bottom: 0 !important;
		}
	}
	&.dropdown {
		.jq-selectbox__dropdown {
			padding-top: 52px;
			padding-bottom: 16px;
			top: 0 !important;
		}
	}
	&.select-mini {
		&.dropup {
			.jq-selectbox__dropdown {
				padding-bottom: 35px;
			}
		}
		&.dropdown {
			.jq-selectbox__dropdown {
				padding-top: 35px;
			}
		}
		.jq-selectbox__select {
			height: 30px;
			padding: 0 30px 0 15px;
		}
		.jq-selectbox__select-text {
			.font-size(13);
			line-height: 28px;
		}
		.jq-selectbox__trigger {
			width: 30px;
		}
		.jq-selectbox__trigger-arrow {
			margin-top: -2px;
			margin-right: -4px;
			.svg-icon-param('icon-expand', 8px, 4px, '[fill]: #423D3D');
		}
		.jq-selectbox__dropdown {
			ul {
				li {
					padding: 5px 15px;
				}
			}
		}
	}
	&.select-sort {
		.jq-selectbox__select {
			height: 35px;
		}
		.jq-selectbox__select-text {
			.font-size(14);
			line-height: 33px;
		}
	}
	&__select {
		height: 38px;
		border: 1px solid #E5E5E5;
		background: white;
		box-shadow: none;
		border-radius: 20px;
		text-shadow: none;
		color: #423D3D;
		z-index: 2;
		padding: 0 34px 0 20px;
		transition: background .2s ease;
		&:hover {
			background: white;
		}
		&:active {
			box-shadow: none;
			background: white;
		}
	}
	&__select-text {
		.regular();
		.font-size(15);
		line-height: 36px;
		color: #423D3D;
		text-align: left;
	}
	&__trigger {
		border: 0;
	}
	&__trigger-arrow {
		border: 0;
		top: 50%;
		margin-top: -3px;
		right: 50%;
		margin-right: -5px;
		.svg-icon-param('icon-expand', 10px, 6px, '[fill]: #423D3D');
	}
	&__dropdown {
		width: 100%;
		margin: 0;
		text-align: left;
		background: #FFFFFF;
		border: 1px solid #E5E5E5;
		box-sizing: border-box;
		box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.05);
		border-radius: 20px;
		ul {
			border-radius: 20px;
			max-height: 300px;
			li {
				padding: 6px 20px;
				white-space: normal;
				.regular();
				.font-size(15);
				color: #828282;
				line-height: 1.2;
				&:hover {
					background: none;
					color: #423D3D;
				}
				&.selected {
					color: #63C2EF;
					background: none;
				}
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.jq-selectbox {
		&.select-mini {
			&.dropup {
				.jq-selectbox__dropdown {
					padding-bottom: 52px;
				}
			}
			&.dropdown {
				.jq-selectbox__dropdown {
					padding-top: 52px;
				}
			}
			.jq-selectbox__select {
				height: 35px;
				padding: 0 30px 0 20px;
			}
			.jq-selectbox__select-text {
				.font-size(14);
				line-height: 33px;
			}
			.jq-selectbox__trigger {
				width: 30px;
			}
			.jq-selectbox__trigger-arrow {
				margin-top: -3px;
				margin-right: -5px;
				.svg-icon-param('icon-expand', 10px, 6px, '[fill]: #423D3D');
			}
			.jq-selectbox__dropdown {
				ul {
					li {
						padding: 6px 20px;
					}
				}
			}
		}
		&__select {
			height: 35px;
		}
		&__select-text {
			.font-size(14);
			line-height: 33px;
		}
		&__dropdown {
			ul {
				li {
					.font-size(14);
				}
			}
		}
	}
}

@media screen and (max-width: 620px) {
	.jq-checkbox {
		width: 20px;
		height: 20px;
	}
}