.rating-info {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: stretch;
	flex-wrap: wrap;
	padding-left: 20px;
	margin-bottom: 40px;
	&__top {
		display: none;
		text-align: center;
		margin-bottom: 25px;
	}
	&__rating {
		margin-bottom: 25px;
	}
	&__col {
		margin-right: 40px;
		&_left {}
		&_center {
			.rating-info__rating {
				display: none;
				margin-right: 0;
			}
		}
		&_right {
			margin-right: 0;
		}
	}
	&__title {
		.regular();
		.font-size(16);
		line-height: 1.2;
		color: #423D3D;
		margin-bottom: 10px;
	}
}

@media screen and (max-width: 980px) {
	.rating-info {
		justify-content: space-between;
		&__col {
			margin-right: 0;
		}
	}
}

@media screen and (max-width: 768px) {
	.rating-info {
		flex-direction: column;
		padding: 0 20px;
		margin-bottom: 25px;
		&__top {
			display: block;
		}
		&__col {
			margin: 0;
			&_left {
				.rating-info__rating {
					display: none;
				}
			}
			&_center {
				display: flex;
				flex-direction: row;
				flex-wrap: wrap;
				align-items: center;
				justify-content: flex-start;
				margin-bottom: 25px;
				.circle-rating {
					margin-right: 40px;
				}
				.rating-info__rating {
					display: block;
				}
			}
			.btn {
				display: none;
			}
		}
		&__rating {
			margin-bottom: 0;
		}
		&__title {
			width: 100%;
			margin-bottom: 15px;
		}
	}
}

@media screen and (max-width: 400px) {
	.rating-info {
		&__col {
			&_center {
				justify-content: space-between;
				.circle-rating {
					margin-right: 0;
				}
			}
		}
	}
}