.rating {
	width: 87px;
	height: 14px;
	position: relative;
	background: url(../img/rating.svg) repeat-x;
	&[data-rate="0"] {
		.rating__fill {
			width: 0;
		}
	}
	&[data-rate="1"] {
		.rating__fill {
			width: 20%;
		}
	}
	&[data-rate="2"] {
		.rating__fill {
			width: 40%;
		}
	}
	&[data-rate="3"] {
		.rating__fill {
			width: 60%;
		}
	}
	&[data-rate="4"] {
		.rating__fill {
			width: 80%
		}
	}
	&[data-rate="5"] {
		.rating__fill {
			width: 100%
		}
	}
	&__fill {
		height: 100%;
		width: 0;
		background: url(../img/rating-fill.svg) repeat-x;
	}
	&_product {
		width: 155px;
		height: 24px;
		background-size: 32px;
		margin-right: 10px;
		.rating__fill {
			background-size: 32px;
		}
	}
	&_review {
		margin-right: 0;
		margin-bottom: 25px;
	}
	&_author {
		display: none;
		margin-right: 40px;
		width: 127px;
		height: 21px;
		background-size: 26px;
		.rating__fill {
			background-size: 26px;
		}
	}
}

@media screen and (max-width: 768px) {
	.rating {
		&_author {
			display: block;
		}
	}
}