.vin-about {
	background: #FFFFFF;
	border: 1px solid #FFFFFF;
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0);
	border-radius: 5px;
	transition: border-color .2s ease, box-shadow .2s ease;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: flex-start;
	padding: 18px;
	margin-bottom: 20px;
	&:hover {
		border-color: #EFEFEF;
		box-shadow: 0px 0px 15px rgba(0, 0, 0, .05);
	}
	&__item {
		position: relative;
		padding: 0 25px;
		&:last-child {
			&:after {
				display: none;
			}
		}
		&:after {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			margin-top: -6px;
			right: 8px;
			.svg-icon-param('icon-forward', 14px, 12px, '[fill]: #423D3D');
		}
	}
	&__item-title {
		.regular();
		.font-size(18);
		line-height: 1.2;
		color: #423D3D;
		margin-bottom: 5px;
	}
	&__item-descr {
		.regular();
		.font-size(16);
		line-height: 1.4;
		color: #797979;
	}
}

@media screen and (max-width: 980px) {
	.vin-about {
		flex-direction: column;
		justify-content: center;
		align-items: center;
		&__item {
			text-align: center;
			padding: 0;
			margin-bottom: 50px;
			&:last-child {
				margin-bottom: 0;
			}
			&:after {
				top: auto;
				right: auto;
				margin-top: auto;
				bottom: -30px;
				left: 50%;
				margin-left: -6px;
				transform: rotate(90deg);
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.vin-about {
		margin: 0 20px 20px;
	}
}

@media screen and (max-width: 620px) {
	.vin-about {
		padding: 20px 25px;
	}
}