.catalog-nav {
	display: none;
	position: absolute;
	top: 100%;
	background: white;
	left: 0;
	width: 250px;
	padding: 10px 0;
	&_lvl-2 {
		display: none;
		top: 0;
		left: 100%;
		width: 200px;
		background: #F8F8F8;
		.catalog-nav__link {
			color: #63C2EF;
			&:after {
				display: none;
			}
		}
		.catalog-nav__link_last {
			color: #797979;
			&:hover {
				color: #797979;
			}
		}
		.catalog-nav__link-text {
			text-decoration: underline;
		}
	}
	&__item {
		&_is-hover {
			> .catalog-nav__link {
				color: #63C2EF;
				&:after {
					.svg-icon-param('icon-arrow-right', 7px, 12px, '[fill]: #63C2EF');
				}
			}
		}
	}
	&__link {
		.regular();
		.font-size(15);
		color: #423D3D;
		line-height: 1.2;
		text-decoration: none;
		padding: 11px 20px;
		display: block;
		position: relative;
		transition: color .2s ease;
		&:after {
			content: '';
			display: block;
			position: absolute;
			.svg-icon-param('icon-arrow-right', 7px, 12px, '[fill]: #423D3D');
			top: 50%;
			margin-top: -6px;
			right: 20px;
			transition: background .2s ease;
		}
		&:hover {
			color: #63C2EF;
			&:after {
				.svg-icon-param('icon-arrow-right', 7px, 12px, '[fill]: #63C2EF');
			}
		}
		.icon {
			display: inline-block;
			vertical-align: middle;
			&.icon-forward {
				.svg-icon('icon-forward', 9px, 8px);
			}
		}
	}
	&__link-text {
		&:hover {
			text-decoration: none;
		}
	}
	&__link-count {
		.regular();
		.font-size(11);
		line-height: 1.2;
		color: #797979;
		display: inline-block;
		padding: 2px 7px;
		background: #E5E5E5;
		border-radius: 20px;
		margin: 2px;
	}
	&__back {
		display: block;
		position: absolute;
		z-index: -1;
		top: 0;
		left: 0;
		width: 100%;
		background: #F8F8F8;
	}
}