.characteristics-full {
	&__section {
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__title {
		.regular();
		.font-size(16);
		color: #423D3D;
		padding: 0 20px;
		margin-bottom: 10px;
	}
	&__row {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
		.regular();
		.font-size(16);
		line-height: 1.2;
		color: #797979;
		&:nth-child(odd) {
			background: white;
		}
		&:nth-child(even) {
			background: #F7F7F7;
		}
	}
	&__name {
		padding: 10px 20px;
		width: 50%;
		color: #63C2EF;
	}
	&__value {
		padding: 10px 20px;
		width: 50%;
	}
}

.faq {
	position: relative;
	display: inline-block;
	&__trigger {
		display: inline-block;
		vertical-align: -2px;
		margin-left: 2px;
		.svg-icon('icon-faq', 14px, 14px);
	}
}

@media screen and (max-width: 768px) {
	.characteristics-full {
		margin-bottom: 20px;
		&__section {
			margin-bottom: 15px;
		}
		&__title {
			.font-size(14);
			padding: 0 10px;
			margin-bottom: 8px;
		}
		&__row {
			position: relative;
			.font-size(14);
		}
		&__name {
			padding: 9px 10px;
		}
		&__value {
			padding: 9px 10px;
		}
	}

	.faq {
		position: static;
		&__trigger {
			.svg-icon('icon-faq', 18px, 18px);
		}
	}
}