.gallery {
	margin-bottom: 5px;
	&_full {
		position: relative;
		margin-bottom: 0;
		.gallery__slides {
			margin: 0 60px;
		}
		.gallery__slide {
			height: 500px;
		}
	}
	&__slide {
		height: 265px;
	}
	&__video-align {
		height: 100%;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__video-wrap {
		width: 100%;
		.aspect-ratio(16, 9);
	}
	&__video-cover {
		iframe {
			width: 100%;
			height: 100%;
		}
	}
	&__cover {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		width: 100%;
	}
	&__image {
		max-width: 100%;
		max-height: 100%;
	}
	&__nav {
		appearance: none;
		padding: 0;
		border: 0;
		box-shadow: none;
		overflow: hidden;
		width: 60px;
		height: 100%;
		background: white;
		cursor: pointer;
		position: absolute;
		top: 0;
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			margin-top: -12.5px;
			left: 50%;
			margin-left: -7.5px;
			transition: background .2s ease;
		}
		&_prev {
			left: -23px;
			&:after {
				.svg-icon-param('icon-prev', 15px, 25px, '[fill]: #C4C4C4');
			}
			&:hover {
				&:after {
					.svg-icon-param('icon-prev', 15px, 25px, '[fill]: #1F9BD7');
				}
			}
		}
		&_next {
			right: -23px;
			&:after {
				.svg-icon-param('icon-next', 15px, 25px, '[fill]: #C4C4C4');
			}
			&:hover {
				&:after {
					.svg-icon-param('icon-next', 15px, 25px, '[fill]: #1F9BD7');
				}
			}
		}
	}
	&__dots {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		margin: 23px 0;
		li {
			margin: 3px;
			&.slick-active {
				button {
					background-color: #1F9BD7;
				}
			}
			button {
				appearance: none;
				margin: 0;
				padding: 0;
				background: none;
				box-shadow: none;
				border-radius: 0;
				border: 0;
				cursor: pointer;
				display: block;
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background: rgba(0, 0, 0, 0.2);
				transition: background-color .2s ease;
				text-indent: -9999px;
			}
		}
	}
}

@media screen and (max-width: 980px) {
	.gallery {
		&_full {
			.gallery__slides {
				margin: 0 60px;
			}
			.gallery__slide {
				height: 350px;
			}
		}
	}
}

@media screen and (max-width: 620px) {
	.gallery {
		margin-bottom: 0;
		&__slide {
			height: 303px;
		}
	}
}

@media screen and (max-width: 400px) {
	.gallery {
		&__slide {
			height: 220px;
		}
	}
}