.section {
	&__title {
		.days();
		.font-size(24);
		line-height: normal;
		color: #423D3D;
		margin-bottom: 50px;
		&_link {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			flex-wrap: wrap;
		}
	}
	&__title-name {
		margin-right: 20px;
	}
	&__more {
		.regular();
		.font-size(15);
		text-decoration: underline;
		color: #63C2EF;
		&:hover {
			text-decoration: none;
		}
	}
	&__footer {
		text-align: center;
		padding-top: 25px;
	}
	&_tabs {
		padding: 28px 0 50px;
	}
	&_advantages {
		padding: 36px 0;
	}
	&_brands {
		padding: 40px 0;
		background: #FBFBFB;
	}
	&_about {
		padding: 40px 0 80px;
	}
	&_in-catalog {
		display: none;
		padding-top: 10px;
		padding-bottom: 50px;
		.section__title {
			margin-bottom: 25px;
			padding: 0 20px;
		}
	}
}

@media screen and (max-width: 768px) {
	.section {
		&__title-name {
			width: 100%;
			margin-right: 0;
		}
		&__more {
			display: none;
		}
		&_last-viewed-items {
			> .container {
				padding: 0;
			}
			.section__title {
				padding: 0 20px;
				margin-bottom: 25px;
			}
		}
		&_in-catalog {
			display: block;
		}
	}
}

@media screen and (max-width: 400px) {
	.section {
		&__title {
			.font-size(18);
		}
		&_tabs {
			padding: 25px 0;
		}
		&_advantages {
			padding: 25px 0;
		}
		&_brands {
			padding: 25px 0;
		}
		&_about {
			padding: 25px 0;
		}
	}
}