.collapsing-block {
	display: none;
	width: 100%;
	&_collapse {
		.collapsing-block__heading {
			&:after {
				transform: rotate(-90deg);
			}
			&:before {
				opacity: 1;
				visibility: visible;
			}
		}
	}
	&_payment {
		.collapsing-block__content {
			padding: 25px 20px 0;
		}
	}
	&_last {
		margin-bottom: 50px;
		.collapsing-block__content {
			padding-bottom: 0;
		}
	}
	&__heading {
		padding: 0 20px;
		background: #FBFBFB;
		height: 60px;
		line-height: 60px;
		position: relative;
		.regular();
		.font-size(16);
		color: #797979;
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			margin-top: -6px;
			right: 20px;
			transition: transform .2s ease;
			.svg-icon('icon-collapse', 12px, 12px);
		}
		&:before {
			content: '';
			width: 100%;
			height: 1px;
			background: #F3F3F3;
			position: absolute;
			left: 0;
			bottom: 0;
			opacity: 0;
			visibility: hidden;
			transition: visibility .2s ease, opacity .2s ease;
		}
	}
	&__count {
		.regular();
		text-align: center;
		.font-size(12);
		color: #797979;
		background: #EFEFEF;
		border-radius: 10px;
		padding: 3px 7px;
		margin-left: 7px;
		line-height: normal;
	}
	&__content {
		padding: 25px 20px 50px;
		&_full {
			padding: 25px 0 50px;
		}
	}
	&__content-title {
		.regular();
		.font-size(16);
		color: #423D3D;
		line-height: 1.2;
		margin-bottom: 15px;
	}
	&__bottom {
		text-align: center;
		padding-top: 5px;
	}
}

@media screen and (max-width: 768px) {
	.collapsing-block {
		display: block;
		&_payment {
			display: none;
		}
	}
}

@media screen and (max-width: 620px) {
	.collapsing-block {
		&_payment {
			display: block;
		}
	}
}

@media screen and (max-width: 400px) {
	.collapsing-block {
		&__bottom {
			padding-top: 25px;
		}
	}
}