.characteristics {
	margin-bottom: 10px;
	&__col {
		width: 50%;
		padding-right: 20px;
	}
	&__row {
		flex-direction: row;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
		.regular();
		.font-size(12);
		line-height: 1.2;
		margin-bottom: 3px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__name {
		color: #797979;
		margin-right: 4px;
	}
	&__value {
		color: #423D3D;
	}
}

@media screen and (max-width: 768px) {
	.characteristics {
		&__col {
			width: 100%;
			padding-right: 0;
			&:first-child {
				margin-bottom: 3px;
			}
		}
	}
}