.footer {
	&__top {
		padding: 20px 0;
		background: #F8F8F8;
		.container {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: flex-start;
		}
	}
	&__col {
		padding-right: 20px;
		&:first-child {
			max-width: 270px;
		}
		&_nav {
			max-width: 170px;
		}
		&_subscribe {
			flex-basis: 250px;
			max-width: 250px;
			padding-right: 0;
			.footer__row {
				margin-bottom: 15px;
			}
			.btn {
				.font-size(13);
				padding: 8px 16px;
			}
		}
		&:not(:first-child) {
			padding-top: 10px;
		}
	}
	&__row {
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__field {
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__input {
		height: 30px;
		padding: 0 15px;
		.font-size(13);
	}
	&__logo-link {
		text-decoration: none;
	}
	&__logo {
		max-width: 150px;
		vertical-align: middle;
	}
	&__address {
		.regular();
		.font-size(11);
		line-height: 1.4;
		color: #797979;
	}
	&__full-link {
		width: calc(~"100% + 40px");
		.regular();
		line-height: 1.2;
		.font-size(14);
		margin: 0 -20px;
		text-align: center;
		background: #EFEFEF;
		color: #797979;
		text-decoration: none;
		padding: 16px 20px;
		display: none;
		transition: background .2s ease;
		&:hover {
			text-decoration: underline;
			background: darken(#EFEFEF, 5%);
		}
	}
	&__phone {
		.regular();
		.font-size(16);
		line-height: normal;
		color: #423D3D;
		display: inline-block;
		margin-bottom: 3px;
		text-decoration: none;
	}
	&__info {
		.regular();
		.font-size(11);
		line-height: normal;
		color: #797979;
	}
	&__email {
		.regular();
		.font-size(12);
		line-height: normal;
		text-decoration: underline;
		color: #423D3D;
		&:hover {
			text-decoration: none;
		}
	}
	&__col-title {
		.regular();
		.font-size(14);
		line-height: normal;
		color: #423D3D;
		margin-bottom: 15px;
	}
	&__link {
		.regular();
		.font-size(12);
		line-height: normal;
		text-decoration: underline;
		color: #423D3D;
		&:hover {
			text-decoration: none;
		}
	}
	&__bottom {
		background: #423D3D;
		padding: 9px 0;
		.container {
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			align-items: flex-start;
		}
	}
	&__copyright {
		.regular();
		.font-size(12);
		line-height: 1.1;
		color: #FFFFFF;
		max-width: 265px;
		margin-right: 30px;
		align-self: center;
	}
	&__logos-wrap {
		flex-grow: 20;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: flex-start;
	}
	&__brands {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		flex-wrap: wrap;
		max-width: 450px;
	}
	&__brand {
		vertical-align: middle;
		max-width: 100%;
		margin: 5px 20px 5px 0;
		&:last-child {
			margin-right: 0;
		}
	}
	&__payments {
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		align-items: center;
		flex-wrap: wrap;
		max-width: 200px;
	}
}

@media screen and (max-width: 980px) {
	.footer {
		&__col {
			padding-right: 0;
			&_nav {
				display: none;
			}
		}
		&__logos-wrap {
			justify-content: flex-end;
		}
		&__payments {
			display: none;
		}
	}
}

@media screen and (max-width: 768px) {
	.footer {
		&__copyright {
			margin-right: 10px;
		}
		&__brands {
			justify-content: flex-end;
			max-width: 400px;
		}
	}
}

@media screen and (max-width: 620px) {
	.footer {
		&__top {
			padding: 0 0 25px;
			.container {
				flex-direction: column;
				align-items: center;
				text-align: center;
			}
		}
		&__col {
			width: 100%;
			&:not(:first-child) {
				padding-top: 0;
			}
			&:first-child {
				max-width: none;
				.footer__row {
					&:first-child {
						display: none;
					}
					&:nth-child(2) {
						margin-bottom: 25px;
					}
				}
			}
			&_subscribe {
				flex-basis: auto;
				max-width: none;
				.footer__col-title {
					display: none;
				}
				.footer__row {
					&:nth-child(2) {
						display: none;
					}
					&:nth-child(3) {
						margin-top: 20px;
						margin-bottom: 0;
					}
					&:last-child {
						display: none;
					}
				}
			}
		}
		&__row {
			margin-bottom: 20px;
		}
		&__address {
			display: none;
		}
		&__full-link {
			display: block;
		}
		&__phone {
			.font-size(24);
		}
		&__info {
			.font-size(13);
		}
		&__email {
			.font-size(14);
		}
		&__copyright {
			display: none;
		}
		&__logos-wrap {
			justify-content: center;
			max-width: 300px;
			margin: 0 auto;
		}
		&__brands {
			width: 100%;
			justify-content: space-between;
			max-width: none;
		}
		&__brand {
			margin-right: 0;
		}
	}
}