.form {}

.form-control {
	&_in-popup {
		.form-control__section {
			margin-bottom: 15px;
			&.form-control__section_mb30 {
				margin-bottom: 30px;
			}
		}
		.form-control__section-title {
			width: 120px;
			flex-shrink: 0;
		}
	}
	&__section {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		margin-bottom: 40px;
		&_heading {
			margin-bottom: 20px;
			align-items: center;
			.form-control__content {
				flex-basis: 460px;
			}
		}
		&:last-child {
			margin-bottom: 0;
		}
		&_mb30 {
			margin-bottom: 30px;
		}
	}
	&__section-title {
		width: 160px;
		padding-top: 8px;
		flex-shrink: 0;
	}
	&__content {
		flex-basis: 570px;
		&_field {
			.form-control__field {
				max-width: 280px;
			}
			.btn {
				width: 100%;
			}
		}
	}
	&__label {
		.regular();
		.font-size(15);
		color: #423D3D;
		line-height: 1.2;
		display: inline-flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		cursor: pointer;
		user-select: none;
		&_small-text {
			.form-control__label-text {
				.font-size(13);
				line-height: 1.4;
			}
		}
	}
	&__field {
		&_mb12 {
			margin-bottom: 12px;
		}
		&_mb20 {
			margin-bottom: 20px;
		}
		&_mb40 {
			margin-bottom: 40px;
		}
		&_centered {
			text-align: center;
		}
		&_select-cover {
			width: 170px;
		}
	}
	&__select-group {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: flex-start;
		.form-control__field {
			margin-right: 28px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	&__checkbox-group {
		.form-control__field {
			margin: 0 20px 15px 0;
		}
	}
	&__checkbox-group-line {
		display: inline-flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__input {
		appearance: none;
		border-radius: 0;
		border: 0;
		box-shadow: none;
		background: none;
		padding: 0;
		margin: 0;
		&::placeholder {
			color: #797979;
		}

		background: #FFFFFF;
		border: 1px solid #E5E5E5;
		border-radius: 20px;
		height: 38px;
		padding: 0 20px;

		.regular();
		.font-size(15);
		line-height: normal;
		color: #423D3D;
		width: 100%;
		text-overflow: ellipsis;
		text-align: left;
	}
	&__textarea {
		appearance: none;
		border-radius: 0;
		border: 0;
		box-shadow: none;
		background: none;
		padding: 0;
		margin: 0;
		&::placeholder {
			color: #797979;
		}
		display: block;
		background: #FFFFFF;
		border: 1px solid #E5E5E5;
		border-radius: 5px;
		width: 100%;
		min-height: 100px;
		padding: 10px 20px;
		.regular();
		.font-size(15);
		color: #423D3D;
		line-height: 1.4;
	}
	&__checkbox-cover {
		flex-shrink: 0;
		margin-right: 10px;
	}
	&__checkbox {}
	&__label-link {
		color: #63C2EF;
		&:hover {
			text-decoration: none;
		}
	}
}

@media screen and (max-width: 768px) {
	.form-control {
		&_in-popup {
			.form-control__section {
				margin-bottom: 0;
				&.form-control__section_mb30 {
					margin-bottom: 0;
					border-bottom: 0;
				}
				&:last-child {
					padding-bottom: 25px;
				}
			}
		}
		&__section {
			padding: 25px 20px;
			border-bottom: 1px solid #F7F7F7;
			margin-bottom: 0;
			&:last-child {
				padding-bottom: 0;
				border-bottom: 0;
			}
			&_heading {
				padding-top: 0;
				border-bottom: 0;
				padding-bottom: 0;
				flex-direction: column;
				margin-bottom: 25px;
				.form-control__content {
					flex-basis: auto;
					width: 100%;
				}
			}
		}
		&__select-group {
			flex-direction: column;
			.form-control__field {
				margin-right: 0;
				margin-bottom: 20px;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
		&__field {
			&_select-cover {
				width: 280px;
			}
		}
		&__input {
			height: 35px;
		}
	}
}

@media screen and (max-width: 620px) {
	.form-control {
		&__section {
			flex-direction: column;
			margin-bottom: 0;
			&_heading {
				margin-bottom: 25px;
				.form-control__content {
					text-align: center;
				}
				.form-control__field_mb12 {
					margin-bottom: 15px;
				}
			}
		}
		&__section-title {
			margin-bottom: 25px;
			width: 100%;
			padding-top: 0;
		}
		&__content {
			flex-basis: auto;
			width: 100%;
			&_field {
				.form-control__field {
					max-width: none;
					width: 100%;
				}
			}
		}
		&__checkbox-group {
			.form-control__field {
				margin: 0 0 15px 0;
			}
		}
		&__checkbox-group-line {
			display: flex;
			flex-direction: column;
			margin-bottom: 0;
			&:last-child {
				.form-control__field {
					&:last-child {
						margin-bottom: 0;
					}
				}
			}
		}
		&__field {
			&_select-cover {
				width: 100%;
			}
		}
	}
}
