.vin-content {
	&__form-img {
		max-width: 290px;
		margin-right: 30px;
	}
	&__search-vin {
		.regular();
		.font-size(15);
		line-height: 1.2;
		color: #63C2EF;
		text-decoration: underline;
		padding: 0 20px;
		&:hover {
			text-decoration: none;
		}
	}
}

@media screen and (max-width: 768px) {
	.vin-content {
		&__form-img {
			max-width: 320px;
			margin-right: 0;
		}
	}
}

@media screen and (max-width: 620px) {
	.vin-content {
		&__search-vin {
			.font-size(16);
			padding: 0;
		}
	}
}