.link-back {
	display: none;
	background: #FBFBFB;
	width: 100%;
	&__link {
		padding: 0 20px 0 40px;
		height: 60px;
		line-height: 60px;
		.regular();
		.font-size(14);
		color: #797979;
		text-decoration: none;
		display: block;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: 50%;
			margin-top: -5px;
			left: 20px;
			display: block;
			.svg-icon-param('icon-arrow-left', 6px, 10px, '[fill]: #797979');
		}
	}
}

@media screen and (max-width: 768px) {
	.link-back {
		display: block;
	}
}