.shipping-about {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: stretch;
	margin-bottom: 40px;
	&_block {
		margin-bottom: 0;
		display: block;
	}
	&__col {
		background: #FFFFFF;
		border: 1px solid #FFFFFF;
		box-shadow: 0px 0px 15px rgba(0, 0, 0, 0);
		border-radius: 5px;
		transition: border-color .2s ease, box-shadow .2s ease;
		flex-basis: 460px;
		&_places {
			margin-right: 20px;
		}
		&_companies {
			.shipping-info__list {
				margin-bottom: 15px;
			}
		}
		&:hover {
			border-color: #EFEFEF;
			box-shadow: 0px 0px 15px rgba(0, 0, 0, .05);
		}
	}
	&__item {
		padding: 20px;
	}
	&__title-wrap {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
		margin-bottom: 20px;
	}
	&__title {
		.regular();
		.font-size(18);
		line-height: 1.2;
		color: #423D3D;
		margin-right: 15px;
	}
	&__region {
		text-decoration: none;
		.regular();
		.font-size(15);
		line-height: 1.2;
		color: #63C2EF;
		.icon {
			display: inline-block;
			vertical-align: middle;
			&.icon-mark {
				.svg-icon('icon-mark', 16px, 16px);
			}
		}
		&:hover {
			.shipping-about__region-name {
				border-bottom-color: transparent;
			}
		}
	}
	&__region-name {
		text-decoration: none;
		color: #63C2EF;
		border-bottom: 1px dashed #63C2EF;
		transition: border-color .2s ease;
	}
}

@media screen and (max-width: 768px) {
	.shipping-about {
		display: flex;
		flex-direction: column;
		&_mobile {
			padding: 0 20px;
		}
		&__col {
			flex-basis: auto;
			margin: 0 20px;
			&_places {
				margin-bottom: 20px;
			}
		}
	}
}

@media screen and (max-width: 620px) {
	.shipping-about {
		margin-bottom: 50px;
		&_block {
			display: none;
		}
		&_in-collapse {
			display: block;
			margin-bottom: 0;
		}
		&__col {
			border: 0;
			border-radius: 0;
			box-shadow: none;
			margin: 0;
			&:hover {
				border-color: white;
				box-shadow: none;
			}
			&_places {
				border-bottom: 1px solid #F7F7F7;
				margin-right: 0;
				.shipping-about__item {
					padding: 0 0 25px 0;
				}
			}
			&_companies {
				.shipping-about__item {
					padding: 25px 0 0;
				}
				.shipping-about__title {
					margin-bottom: 0;
				}
			}
		}
		&__title-wrap {
			flex-direction: column;
			margin-bottom: 25px;
		}
		&__title {
			.font-size(16);
			margin-bottom: 25px;
			padding: 0 20px;
			width: 100%;
		}
		&__region {
			display: block;
			padding: 0 20px;
			width: 100%;
			margin: 0;
			.font-size(16);
			background: #FBFBFB;
			height: 60px;
			line-height: 60px;
			position: relative;
			.icon {
				margin-right: 5px;
				&.icon-mark {
					.svg-icon('icon-mark', 20px, 20px);
				}
			}
			&:after {
				content: '';
				display: block;
				position: absolute;
				top: 50%;
				margin-top: -5px;
				right: 20px;
				.svg-icon-param('icon-arrow-right', 6px, 10px, '[fill]: #1F9BD7');
			}
		}
		&__region-name {
			border-bottom: 0;
			vertical-align: middle;
		}
	}
}