.brands {
	.slick-track {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
	}
	&__nav {
		appearance: none;
		margin: 0;
		padding: 0;
		background: none;
		box-shadow: none;
		border-radius: 0;
		border: 0;
		cursor: pointer;
		width: 30px;
		height: 40px;
		position: absolute;
		top: 50%;
		z-index: 1;
		margin-top: -20px;
		&:after {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: -14px;
			margin-left: -7px;
		}
		&_prev {
			left: -8px;
			&:after {
				.svg-icon('icon-prev', 14px, 28px);
			}
		}
		&_next {
			right: -8px;
			&:after {
				.svg-icon('icon-next', 14px, 28px);
			}
		}
	}
	&__wrap {
		position: relative;
	}
	&__slides {
		margin-bottom: 57px;
	}
	&__slide {
		text-align: center;
	}
	&__link {
		text-decoration: none;
		display: inline-block;
		vertical-align: middle;
	}
	&__image {
		max-width: 170px;
		display: inline;
	}
	&__bottom {
		text-align: center;
	}
}