.main-slider {
	height: 380px;
	&__slide {
		// background:  repeat;
		background-image: url(../img/pattern.svg), linear-gradient(0deg, #FBFBFB, #FBFBFB);
		overflow: hidden;
		.container {
			height: 100%;
		}
	}
	&__slide-wrap {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		padding-top: 30px;
		height: 100%;
	}
	&__image-wrap {
		flex-basis: 320px;
		text-align: center;
		align-self: stretch;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
	}
	&__image {
		max-width: 100%;
		max-height: 100%;
		vertical-align: middle;
	}
	&__about {
		flex-grow: 20;
		padding-left: 32px;
		max-width: 65%;
	}
	&__title-wrap {
		width: 100%;
		position: relative;
	}
	&__skew-line {
		box-shadow: 0px 0px 15px rgba(0, 0, 0, .05);
		background: white;
		transform: skew(-45deg);
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		width: 1000%;
		z-index: 2;
		&_blue {
			background: #C8EDFF;
			z-index: 1;
			transform: skew(24deg);
			left: 20px;
		}
	}
	&__title {
		.days();
		.font-size(48);
		color: #423D3D;
		line-height: 1.3;
		width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		z-index: 3;
		position: relative;
		padding-left: 50px;
	}
	&__sub-title-wrap {
		width: 100%;
		position: relative;
		margin-bottom: 15px;
	}
	&__sub-title {
		.light();
		.font-size(24);
		color: #1F9BD7;
		line-height: 1.2;
		position: relative;
		padding: 11px 0 11px 50px;
		z-index: 3;
		width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	&__info {
		padding-left: 50px;
	}
	&__descr {
		margin-bottom: 25px;
	}
}

@media screen and (max-width: 980px) {
	.main-slider {
		height: 330px;
		&__title {
			.font-size(35);
			padding-left: 40px;
		}
		&__sub-title {
			.font-size(20);
			padding-left: 40px;
		}
		&__info {
			padding-left: 40px;
		}
	}
}


@media screen and (max-width: 768px) {
	.main-slider {
		&__slide-wrap {
			padding-top: 0;
		}
		&__image-wrap {
			width: 40%;
			flex-basis: auto;
		}
		&__about {
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			width: 60%;
			max-width: none;
		}
		&__descr {
			display: none;
		}
		&__title {
			.font-size(30);
		}
		&__sub-title-wrap {
			margin-bottom: 40px;
		}
		&__sub-title {
			.font-size(16);
		}
	}
}

@media screen and (max-width: 620px) {
	.main-slider {
		height: 280px;
		&__image-wrap {
			width: 34%;
		}
		&__about {
			width: 66%;
			padding-left: 23px;
		}
		&__title {
			.font-size(23);
			padding: 5px 0 5px 25px;
		}
		&__skew-line {
			&_blue {
				left: 10px;
			}
		}
		&__sub-title-wrap {
			margin-bottom: 20px;
		}
		&__sub-title {
			.font-size(12);
			padding: 8px 0 8px 25px;
		}
		&__info {
			padding-left: 25px;
		}
	}
}

@media screen and (max-width: 400px) {
	.main-slider {
		height: auto;
		&__slide-wrap {
			flex-direction: column;
		}
		&__image-wrap {
			width: 100%;
			padding: 20px 0;
		}
		&__image {
			max-width: auto;
			max-height: 175px;
		}
		&__about {
			width: 100%;
			padding-left: 5%;
			margin-bottom: 55px;
		}
		&__title {
			width: 85%;
			margin: 0 auto;
			padding: 5px 0;
			text-align: center;
		}
		&__skew-line {
			&_blue {
				left: 10px;
			}
		}
		&__sub-title-wrap {
			margin-bottom: 20px;
		}
		&__sub-title {
			width: 85%;
			margin: 0 auto;
			padding: 8px 0;
			text-align: center;
		}
		&__info {
			padding-left: 0;
			text-align: center;
		}
	}
}

