.pagination {
	text-align: center;
	&_bottom {
		.pagination__list {
			padding-bottom: 0;
			padding-top: 8px;
			&:after {
				bottom: auto;
				top: 0;
			}
		}
		.pagination__link {
			&:before {
				bottom: auto;
				top: -8px;
			}
		}
	}
	&__list {
		display: inline-flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		position: relative;
		padding-bottom: 8px;
		&:after {
			content: '';
			width: calc(~"100% - 60px");
			height: 2px;
			background: #EFEFEF;
			left: 30px;
			bottom: 0;
			position: absolute;
		}
	}
	&__item {
		&_active {
			.pagination__link {
				color: #1F9BD7;
				&:before {
					visibility: visible;
					opacity: 1;
				}
			}
		}
		&:hover {
			.pagination__link {
				color: #1F9BD7;
			}
		}
	}
	&__link {
		text-decoration: none;
		.regular();
		.font-size(15);
		text-align: center;
		color: #797979;
		display: block;
		height: 30px;
		width: 30px;
		line-height: 30px;
		transition: color .2s ease;
		position: relative;
		&:before {
			content: '';
			width: 100%;
			height: 4px;
			background: #1F9BD7;
			position: absolute;
			left: 0;
			bottom: -8px;
			opacity: 0;
			visibility: hidden;
			transition: opacity .2s ease, visibility .2s ease;
			z-index: 1;
		}
		&_arrow {
			position: relative;
			&:before {
				display: none;
			}
			&:after {
				content: '';
				display: block;
				position: absolute;
				top: 50%;
				left: 50%;
				margin-top: -5px;
				margin-left: -3px;
			}
		}
		&_prev {
			&:after {
				.svg-icon('icon-arrow-left', 6px, 10px);
			}
		}
		&_next {
			&:after {
				.svg-icon('icon-arrow-right', 6px, 10px);
			}
		}
	}
}