.shipping-info {
	&_places {}
	&__list {}
	&__item {
		.regular();
		.font-size(15);
		line-height: 1.4;
		color: #423D3D;
		margin-bottom: 15px;
		position: relative;
		padding-left: 12px;
		&:before {
			content: '';
			width: 5px;
			height: 5px;
			border-radius: 50%;
			background: #423D3D;
			position: absolute;
			left: 0;
			top: 8px;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__descr {
		.regular();
		.font-size(15);
		line-height: 1.4;
		color: #797979;
	}
	&__phone {
		color: #797979;
		text-decoration: none;
		transition: color .2s ease;
		&:hover {
			text-decoration: underline;
			color: #423D3D;
		}
	}
}

@media screen and (max-width: 620px) {
	.shipping-info {
		padding: 0 20px;
		&_places {
			.shipping-info__item {
				padding-left: 0;
				&:before {
					display: none;
				}
			}
		}
		&_in-collapse {
			padding: 0;
		}
	}
}