.social-nav {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	&_product {
		justify-content: space-between;
		.social-nav__item {
			margin: 0;
		}

		.icon {
			display: block;
			&.icon-vk {
				.svg-icon('icon-vk', 24px, 24px);
			}
			&.icon-fb {
				.svg-icon('icon-fb', 24px, 24px);
			}
			&.icon-ytube {
				.svg-icon('icon-ytube', 24px, 24px);
			}
			&.icon-od {
				.svg-icon('icon-od', 24px, 24px);
			}
			&.icon-instagram {
				.svg-icon('icon-instagram', 24px, 24px);
			}
			&.icon-tw {
				.svg-icon('icon-tw', 24px, 24px);
			}
		}
	}
	&__item {
		margin-right: 10px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__link {
		text-decoration: none;
	}

	.icon {
		display: block;
		&.icon-vk {
			.svg-icon('icon-vk', 26px, 26px);
		}
		&.icon-fb {
			.svg-icon('icon-fb', 26px, 26px);
		}
		&.icon-ytube {
			.svg-icon('icon-ytube', 26px, 26px);
		}
		&.icon-od {
			.svg-icon('icon-od', 26px, 26px);
		}
		&.icon-instagram {
			.svg-icon('icon-instagram', 26px, 26px);
		}
		&.icon-tw {
			.svg-icon('icon-tw', 26px, 26px);
		}
	}
}

@media screen and (max-width: 980px) {
	.social-nav {
		&_product {
			max-width: 354px;
			margin: 0 auto;
			.icon {
				&.icon-vk {
					.svg-icon('icon-vk', 40px, 40px);
				}
				&.icon-fb {
					.svg-icon('icon-fb', 40px, 40px);
				}
				&.icon-ytube {
					.svg-icon('icon-ytube', 40px, 40px);
				}
				&.icon-od {
					.svg-icon('icon-od', 40px, 40px);
				}
				&.icon-instagram {
					.svg-icon('icon-instagram', 40px, 40px);
				}
				&.icon-tw {
					.svg-icon('icon-tw', 40px, 40px);
				}
			}
		}
	}
}

@media screen and (max-width: 620px) {
	.social-nav {
		justify-content: space-between;
		max-width: 300px;
		margin: 0 auto;
		&_product {
			max-width: 354px;
			padding: 0 20px;
		}
		&__item {
			margin-right: 0;
			&:last-child {
				margin-right: 0;
			}
		}

		.icon {
			&.icon-vk {
				.svg-icon('icon-vk', 40px, 40px);
			}
			&.icon-fb {
				.svg-icon('icon-fb', 40px, 40px);
			}
			&.icon-ytube {
				.svg-icon('icon-ytube', 40px, 40px);
			}
			&.icon-od {
				.svg-icon('icon-od', 40px, 40px);
			}
			&.icon-instagram {
				.svg-icon('icon-instagram', 40px, 40px);
			}
		}
	}
}