.breadcrumbs {
	padding: 20px 0;
	&__list {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		flex-wrap: wrap;
	}
	&__item {
		margin-right: 23px;
		position: relative;
		&:last-child {
			margin-right: 0;
			&:after {
				display: none;
			}
		}
		&:after {
			content: '';
			width: 3px;
			height: 3px;
			border-radius: 50%;
			background: #423D3D;
			position: absolute;
			top: 10px;
			right: -12px;
		}
		&:hover {
			.breadcrumbs__name {
				text-decoration: none;
			}
		}
		&_default {
			.breadcrumbs__name {
				text-decoration: none;
				color: #423D3D;
				margin-right: 0;
			}
		}
	}
	&__link {
		text-decoration: none;
		color: #63C2EF;
	}
	&__name {
		.regular();
		.font-size(14);
		line-height: 1.2;
		text-decoration: underline;
		transition: color .2s ease;
		margin-right: 3px;
		display: inline-block;
		vertical-align: middle;
	}
	.icon {
		display: inline-block;
		vertical-align: middle;
		&.icon-breadcrumb {
			.svg-icon('icon-breadcrumb', 14px, 14px);
		}
	}
}

@media screen and (max-width: 768px) {
	.breadcrumbs {
		display: none;
	}
}