.vote-menu {
	.regular();
	line-height: normal;
	.font-size(15);
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	&_mobile {
		display: none;
	}
	&__text {
		color: #797979;
		margin-right: 20px;
	}
	&__btn {
		appearance: none;
		border: 0;
		padding: 0;
		margin: 0;
		border-radius: 0;
		box-shadow: none;
		background: white;
		cursor: pointer;
		&_like {
			margin-right: 15px;
			.vote-menu__count {
				color: #27AE60;
			}
		}
		&_dislike {
			margin-top: 3px;
			.vote-menu__count {
				color: #F32D2D;
			}
		}
		.icon {
			display: inline-block;
			margin-right: 2px;
			&.icon-like {
				vertical-align: -1px;
				.svg-icon('icon-like', 18px, 16px);
			}
			&.icon-dislike {
				vertical-align: -8px;
				.svg-icon('icon-dislike', 18px, 16px);
			}
		}
	}
	&__count {
		display: inline-block;
		vertical-align: middle;
	}
}

@media screen and (max-width: 768px) {
	.vote-menu {
		display: none;
		&_mobile {
			display: block;
		}
	}
}