.tools-menu {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	margin-right: -10px;
	.header_fixed & {
		margin-right: 0;
	}
	&__item {
		margin: 10px;
		position: relative;
		&_fill {
			.tools-menu__count {
				display: block;
			}
		}
		&:first-child {
			margin-left: 0;
		}
		&:last-child {
			margin-right: 0;
		}
		.header_fixed & {
			margin-top: 0;
			margin-bottom: 0;
			&:last-child {
				margin-right: 10px;
			}
		}
	}
	&__link {
		display: block;
		text-decoration: none;
		padding: 10px;
	}
	&__count {
		display: none;
		border-radius: 50%;
		background: #F32D2D;
		box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
		width: 18px;
		height: 18px;
		z-index: 1;
		position: absolute;
		top: 0;
		right: 0;
		pointer-events: none;

		.regular();
		.font-size(13);
		line-height: 18px;
		text-align: center;
		color: #FFFFFF;
	}

	.icon {
		display: block;
		&.icon-search {
			.svg-icon('icon-search', 16px, 16px);
		}
		&.icon-compare {
			.svg-icon('icon-compare', 20px, 20px);
		}
		&.icon-heart {
			.svg-icon-param('icon-heart', 20px, 20px, '[fill]: #1f9bd7');
		}
		&.icon-cart {
			.svg-icon('icon-cart', 20px, 20px);
		}
	}
}

@media screen and (max-width: 980px) {
	.tools-menu {
		&__item {
			margin: 10px 3px;
			&:last-child {
				margin-right: 10px;
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.tools-menu {
		margin-right: 0;
	}
}