.mobile-menu {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: #FBFBFB;
	z-index: 20;
	overflow: hidden;
	&__head {
		text-align: right;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 2;
		background: #FBFBFB;
	}
	&__close {
		appearance: none;
		padding: 0;
		margin: 0;
		background: none;
		box-shadow: none;
		border: 0;
		border-radius: 0;
		cursor: pointer;
		display: inline-block;

		padding: 23px 25px;
	}
	&__body {
		height: 100%;
		padding-top: 60px;
	}
	&__scroll-wrap {
		padding-bottom: 80px;
		height: 100%;
		overflow-y: auto;
		-webkit-overflow-scrolling: touch;
	}

	.icon {
		display: block;
		&.icon-close {
			.svg-icon('icon-close', 14px, 14px)
		}
	}
}

