.delivery-block {
	padding: 20px;
	background: #FFFFFF;
	border: 1px solid white;
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0);
	border-radius: 5px;
	transition: box-shadow .2s ease, border-color .2s ease;
	text-align: center;
	&:hover {
		border-color: #EFEFEF;
		box-shadow: 0px 0px 15px rgba(0, 0, 0, .05);
	}
	&__info {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__logo-wrap {
		margin-bottom: 10px;
	}
	&__logo {
		max-height: 50px;
		max-width: 100%;
		vertical-align: middle;
	}
	&__link {
		text-decoration: none;
		.regular();
		.font-size(15);
		line-height: 1.2;
		color: #63C2EF;
		border-bottom: 1px dashed #63C2EF;
		transition: border-color .2s ease;
		&:hover {
			border-bottom-color: transparent;
		}
		&_region {
			border-bottom: 0;
		}
		.icon {
			display: inline-block;
			vertical-align: middle;
			&.icon-mark {
				.svg-icon('icon-mark', 16px, 16px);
			}
		}
	}
	&__link-name {
		border-bottom: 1px dashed #63C2EF;
		transition: border-color .2s ease;
		&:hover {
			border-bottom-color: transparent;
		}
	}
	&__title {
		.regular();
		.font-size(16);
		line-height: 1.2;
		color: #423D3D;
		margin-bottom: 10px;
	}
	&__text {
		.regular();
		.font-size(14);
		line-height: 1.2;
		color: #797979;
		margin-bottom: 7px;
		&:last-child {
			margin-bottom: 0;
		}
		&_dark {
			color: #423D3D;
		}
	}
}

@media screen and (max-width: 980px) {
	.delivery-block {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		&__info {
			margin-bottom: 25px;
			width: 100%;
		}
		&__logo {
			max-height: none;
			max-width: 180px;
		}
		&__link {
			.font-size(16);
			margin: 0 10px;
			&_region {
				.icon {
					&.icon-mark {
						.svg-icon('icon-mark', 20px, 20px);
					}
				}
			}
		}
		&__title {
			.font-size(18);
		}
		&__text {
			.font-size(16);
			margin-bottom: 10px;
		}
	}
}

@media screen and (max-width: 768px) {
	.delivery-block {
		margin: 0 20px;
	}
}

@media screen and (max-width: 620px) {
	.delivery-block {
		margin: 0;
		padding: 25px 0;
		border: 0;
		box-shadow: none;
		border-radius: 0;
		&:hover {
			border-color: transparent;
			box-shadow: none;
		}
	}
}

@media screen and (max-width: 400px) {
	.delivery-block {
		&__info {
			&:nth-child(2) {
				.delivery-block__title {
					display: none;
				}
			}
		}
		&__link {
			.font-size(16);
			margin: 0 10px;
			&_region {
				position: relative;
				text-align: left;
				display: block;
				width: 100%;
				height: 60px;
				background: #FBFBFB;
				line-height: 60px;
				padding: 0 20px;
				margin: 0 auto;
				&:after {
					content: '';
					display: block;
					position: absolute;
					top: 50%;
					margin-top: -5px;
					right: 20px;
					.svg-icon-param('icon-arrow-right', 6px, 10px, '[fill]: #1F9BD7');
				}
				.delivery-block__link-name {
					border-bottom: 0;
				}
				.icon {
					vertical-align: -3px;
					margin-right: 5px;
				}
			}
		}
	}
}