.mobile-categories {
	display: none;
	&__title {
		.days();
		.font-size(24);
		line-height: normal;
		color: #423D3D;
		padding: 25px 20px;
	}
	&__body {
		height: 240px;
		overflow-y: hidden;
		&_full {
			height: auto;
		}
	}
	&__list {}
	&__item {
		border-bottom: 1px solid #F7F7F7;
		&:last-child {
			border-bottom: 0;
		}
	}
	&__link {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		text-decoration: none;
		padding: 13px 20px;
		position: relative;
		&:after {
			content: '';
			display: block;
			.svg-icon-param('icon-arrow-right', 7px, 12px, '[fill]: #1F9BD7');
			position: absolute;
			top: 50%;
			margin-top: -6px;
			right: 20px;
		}

		.icon {
			display: block;
			margin-right: 14px;
			&.icon-fog {
				.svg-icon('icon-fog', 34px, 34px);
			}
			&.icon-accessories {
				.svg-icon('icon-accessories', 34px, 34px);
			}
			&.icon-aroma {
				.svg-icon('icon-aroma', 34px, 34px);
			}
			&.icon-electro {
				.svg-icon('icon-electro', 34px, 34px);
			}
			&.icon-sale {
				.svg-icon('icon-sale', 34px, 34px);
			}
			&.icon-led {
				.svg-icon-param('icon-led', 34px, 34px, '[fill]: #1F9BD7');
			}
			&.icon-xenon {
				.svg-icon-param('icon-xenon', 34px, 34px, '[fill]: #1F9BD7');
			}
			&.icon-lights {
				.svg-icon-param('icon-lights', 34px, 34px, '[fill]: #1F9BD7');
			}
			&.icon-lamps {
				.svg-icon-param('icon-lamps', 34px, 34px, '[fill]: #1F9BD7');
			}
		}
	}
	&__link-name {
		.regular();
		.font-size(14);
		line-height: 1.2;
		color: #63C2EF;
		flex-grow: 20;
		user-select: none;
		margin-right: 20px;
	}
	&__count {
		background: #F7F7F7;
		border-radius: 50px;
		padding: 4px 10px;
		margin-right: 22px;
		.font-size(14);
		line-height: normal;
		text-align: center;
		color: #797979;
	}
	&__footer {
		padding: 15px 0;
		text-align: center;
	}


	.btn {
		&.btn_up {
			.icon {
				transform: rotate(-180deg);
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.mobile-categories {
		display: block;
	}
}

@media screen and (max-width: 400px) {
	.mobile-categories {
		&__title {
			.font-size(18);
		}
	}
}