.advantages {
	&__wrap {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
	}
	&__item {
		margin: 0 25px;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
	}
	&__descr {
		.regular();
		.font-size(14);
		line-height: 1.4;
		color: #423D3D;
		display: inline-block;
		vertical-align: middle;
		max-width: 180px;
	}
	&__icon {
		margin-right: 20px;
		max-width: 50px;
	}
}

@media screen and (max-width: 980px) {
	.advantages {
		&__item {
			margin: 0 20px;
		}
		&__icon {
			margin-right: 10px;
			max-width: 40px;
		}
	}
}

@media screen and (max-width: 768px) {
	.advantages {
		&__wrap {
			flex-direction: column;
			align-items: flex-start;
		}
		&__item {
			padding: 20px 0;
			margin: 0;
		}
		&__descr {
			max-width: none;
		}
	}
}