.quick-view {
	&__name {
		.regular();
		.font-size(18);
		line-height: 1.44;
		color: #423D3D;
		margin-bottom: 20px;
	}
	&__main {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
	}
	&__img-wrap {
		flex-basis: 137px;
		max-width: 137px;
		height: 137px;
		flex-shrink: 0;
	}
	&__image {
		vertical-align: middle;
		max-width: 100%;
	}
	&__info {
		flex-grow: 20;
		padding-left: 20px;
	}
	&__price {
		.semi-bold();
		.font-size(30);
		line-height: 1.2;
		color: #423D3D;
	}
	&__old-price {
		.regular();
		.font-size(26);
		line-height: 1.2;
		text-decoration: line-through;
		color: #C4C4C4;
	}
	&__characteristics {
		margin: 10px 0;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		align-items: flex-start;
		justify-content: flex-start;
		.characteristics__col {
			width: auto;
			padding-right: 0;
			flex: 1;
		}
		.characteristics__row {
			.font-size(14);
		}
		.characteristics__name {
			color: #423D3D;
		}
		.characteristics__value {
			color: #423D3D;
		}
	}
	&__rating-wrap {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
	}
	&__rating {
		margin-right: 10px;
		width: 112px;
		height: 18px;
		background-size: 23px;
		.rating__fill {
			background-size: 23px;
		}
	}
	&__reviews {}
	&__reviews-link {
		.regular();
		.font-size(12);
		text-decoration: underline;
		color: #797979;
		&:hover {
			text-decoration: none;
		}
	}
	&__available {
		margin: 24px 0 20px;
	}
	&__available-title {
		.regular();
		.font-size(16);
		line-height: 1.2;
		color: #423D3D;
		margin-bottom: 10px;
	}
	&__available-row {
		margin-bottom: 7px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__address {
		display: inline-block;
		vertical-align: middle;
		.regular();
		.font-size(14);
		line-height: 1.2;
		color: #797979;
		margin-right: 3px;
	}
	&__count {
		display: inline-block;
		vertical-align: middle;
		.regular();
		.font-size(12);
		height: 20px;
		line-height: 20px;
		color: #423D3D;
		background: #F2F2F2;
		border-radius: 10px;
		padding: 0 11px;
	}
	&__footer {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
		margin-bottom: 10px;
		.btn {
			margin-right: 20px;
		}
	}
	&__add-count {
		margin-left: -10px;
	}
	&__features {}
	&__bottom {}
	&__fast-buy {
		.regular();
		.font-size(15);
		line-height: 1.2;
		text-decoration: none;
		border-bottom: 1px dashed #63C2EF;
		transition: border-color .2s ease;
		color: #63C2EF;
		&:hover {
			border-color: transparent;
		}
	}
}