.header {
	&__top {
		background: #FBFBFB;
	}
	&__top-inner-wrap {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: flex-start;
	}
	&__main {
		padding: 16px 0;
		background: white;
		box-shadow: 0px 2px 20px rgba(0, 0, 0, 0);
		.container {
			position: relative;
		}
		&.scroll-to-fixed-fixed {
			position: fixed;
			width: 100%;
			top: 0;
			left: 0;
			z-index: 8;
			padding: 0;
			&:after {
				content: '';
				position: absolute;
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
				box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.1);
				pointer-events: none;
			}
			.header__main-inner {
				align-items: center;
			}
			.header__menu {
				display: block;
			}
			.header__logo-wrap {
				margin-right: 44px;
			}
			.header__logo {
				display: none;
			}
			.header__logo_mini {
				display: block;
			}
			.header__phone-box {
				display: none;
			}
			.header__nav-wrap {
				display: none;
			}
		}
	}
	&__main-inner {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
	}
	&__menu {
		margin-right: 20px;
		position: relative;
		display: none;
	}
	&__hamburger {
		width: 60px;
		height: 60px;
		padding-top: 25px;
		cursor: pointer;
		background: white;
		transition: background .2s ease;
		&_active {
			background: #F8F8F8;
		}
		&:hover {
			background: #F8F8F8;
		}
	}
	&__hamburger-line {
		width: 18px;
		height: 2px;
		background: #1F9BD7;
		margin: 0 auto 3px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__logo-wrap {
		margin-right: 27px;
	}
	&__logo-link {
		text-decoration: none;
	}
	&__logo {
		max-width: 212px;
		vertical-align: middle;
		&_mini {
			display: none;
			max-width: 35px;
		}
	}
	&__middle-wrap {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		flex-grow: 20;
	}
	&__inner-wrap {
		flex-grow: 20;
	}
	&__nav-wrap {
		margin-bottom: 4px;
	}
	&__search-wrap {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
	}
	&__open-search {
		text-decoration: none;
		display: none;
		padding: 14px;
		.icon-search {
			display: block;
			.svg-icon('icon-search', 16px, 16px);
		}
	}
	&__phone-box {
		max-width: 200px;
	}
	&__phone {
		.regular();
		.font-size(20);
		line-height: 1.2;
		text-decoration: none;
		color: #423D3D;
	}
	&__phone-descr {
		margin-top: 3px;
		.regular();
		.font-size(11);
		line-height: normal;
		color: #797979;
	}
	&__tools-wrap {}
}


@media screen and (max-width: 980px) {
	.header {
		&__main {
			&.scroll-to-fixed-fixed {
				.header__main-inner {
					align-items: center;
				}
				.header__hamburger {
					display: block;
				}
				.header__logo-wrap {
					margin-right: 20px;
				}
				.header__logo {
					display: none;
				}
				.header__logo_mini {
					display: block;
				}
				.header__phone-box {
					display: none;
				}
				.header__nav-wrap {
					display: none;
				}
			}
		}
		&__nav-wrap {
			display: none;
		}
		&__phone-box {
			display: none;
		}
		&__menu {
			display: block;
			margin-right: 0;
		}
	}
}

@media screen and (max-width: 768px) {
	.header {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 8;
		box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.1);
		&__top {
			display: none;
		}
		&__main {
			padding: 0;
			z-index: auto;
			position: relative;
			.container {
				padding: 0;
			}
		}
		&__main-inner {
			align-items: center;
		}
		&__hamburger {
			display: block;
		}
		&__logo-wrap {
			margin-right: 20px;
		}
		&__logo {
			display: none;
			&_mini {
				display: block;
			}
		}
	}
}

@media screen and (max-width: 620px) {
	.header {
		&__logo-wrap {
			margin-right: 0;
		}
		&__search-wrap {
			justify-content: flex-end;
		}
		&__open-search {
			display: block;
		}
	}
}