.inner-page {
	padding-bottom: 80px;
	&__title-wrap {
		padding: 40px 0;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		.inner-page__title {
			padding: 0;
			margin: 0;
		}
	}
	&__title {
		.days();
		.font-size(24);
		line-height: 1.5;
		color: #423D3D;
		padding: 20px 0;
		margin-bottom: 20px;
	}
	&__title-link {
		.regular();
		.font-size(15);
		line-height: 1.2;
		text-decoration-: underline;
		color: #63C2EF;
		&:hover {
			text-decoration: none;
		}
	}
	&__content {}
}

@media screen and (max-width: 768px) {
	.inner-page {
		padding-bottom: 50px;
		&__title-wrap {
			padding: 25px 20px;
			.inner-page__title {
				padding: 0;
				margin: 0;
			}
		}
		&__title {
			padding: 25px 20px;
			margin-bottom: 0;
		}
		&__title-link {
			display: none;
		}
	}
}

@media screen and (max-width: 620px) {
	.inner-page {
		&_product {
			.inner-page__title {
				.font-size(21);
				padding-bottom: 10px;
			}
		}
	}
}

@media screen and (max-width: 400px) {
	.inner-page {
		&_product {
			.inner-page__title {
				.font-size(18);
			}
		}
		&__title {
			.font-size(18);
		}
	}
}