.product {
	padding: 20px;
	background: #FFFFFF;
	border: 1px solid white;
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0);
	border-radius: 5px;
	transition: border-color .2s ease, box-shadow .2s ease;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	&:hover {
		border-color: #EFEFEF;
		box-shadow: 0px 0px 15px rgba(0, 0, 0, .05);
	}
	&_mobile {
		display: none;
		padding: 0;
		box-shadow: none;
		border-radius: 0;
		border: 0;
		&:hover {
			box-shadow: none;
			border-color: transparent;
		}
	}
	&__col {
		&_gallery {
			width: 250px;
			margin-right: 30px;
		}
		&_info {
			width: 100%;
			position: relative;
		}
	}
	&__top {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		flex-wrap: wrap;
		margin-bottom: 24px;
		padding-right: 95px;
	}
	&__reviews {
		padding: 5px 0;
	}
	&__reviews-link {
		.regular();
		.font-size(14);
		line-height: 1.2;
		color: #797979;
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}
	&__features-tools {
		position: absolute;
		top: 0;
		right: 0;
		z-index: 2;
	}
	&__articul {
		.regular();
		.font-size(15);
		line-height: 1.2;
		color: #423D3D;
		margin-bottom: 24px;
	}
	&__articul-value {
		color: #797979;
		margin-left: 4px;
	}
	&__characteristics {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
		margin-bottom: 19px;
		.characteristics__row {
			.font-size(16);
			margin-bottom: 5px;
		}
	}
	&__available {
		margin-bottom: 30px;
		.font-size(15);
		.available__name {
			color: #423d3d;
		}
	}
	&__price-box {
		justify-content: flex-start;
		margin-bottom: 24px;
		.price-box__price {
			.font-size(30);
			margin: 0 10px 0 0;
		}
		.price-box__old-price {
			.font-size(26);
			margin: 5px 0 0 0;
		}
	}
	&__buy-wrap {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		flex-wrap: wrap;
		margin-bottom: 24px;
	}
	&__bottom {}
	&__fast-buy {
		.regular();
		.font-size(15);
		line-height: 1.2;
		color: #63C2EF;
		text-decoration: none;
		border-bottom: 1px dashed #63C2EF;
		transition: border-color .2s ease;
		&:hover {
			border-bottom-color: transparent;
		}
	}
	&__gallery-mobile {
		.product-gallery__label {
			right: auto;
			left: 0;
			top: 0;
			height: 26px;
			line-height: 26px;
			width: 54px;
			.font-size(14);
		}
	}
}

@media screen and (max-width: 768px) {
	.product {
		margin: 0 20px;
		&__buy-wrap {
			.btn {
				min-width: 150px;
			}
		}
	}
}

@media screen and (max-width: 620px) {
	.product {
		display: none;
		padding: 0;
		&_mobile {
			display: block;
			margin: 0;
		}
		&__col {
			width: 100%;
			&_gallery {
				margin-right: 0;
				padding: 0 20px 25px;
			}
		}
		&__articul {
			margin-bottom: 13px;
		}
		&__top {
			padding-right: 0;
			margin-bottom: 20px;
		}
		&__features-tools {
			right: -10px;
		}
		&__available {
			margin-bottom: 0;
		}
		&__bottom {
			text-align: center;
			background: #FBFBFB;
			padding: 20px 20px 25px;
		}
		&__price-box {
			justify-content: center;
			margin-bottom: 20px;
			.price-box__price {
				.font-size(30);
				margin: 0 10px 0 0;
			}
			.price-box__old-price {
				.font-size(26);
				margin: 5px 0 0 0;
			}
		}
		&__buy-wrap {
			justify-content: center;
			text-align: center;
			margin-bottom: 20px;
			.btn {
				padding: 12px 15px;
				.font-size(16);
				min-width: 0;
				width: 100%;
				max-width: 280px;
				display: block;
			}
		}
		&__fast-buy {
			.font-size(16);
		}
	}
}