.grid-view {
	position: absolute;
	top: 17px;
	right: 0;
	z-index: 2;
	&__item {
		cursor: pointer;
		display: inline-block;
		vertical-align: top;
		&:first-child {
			margin-right: 15px - 4px;
		}
		.icon {
			display: block;
			transition: background .2s ease;
			&.icon-grid-list {
				.svg-icon('icon-grid-list', 18px, 18px);
			}
			&.icon-grid-block {
				.svg-icon('icon-grid-block', 18px, 18px);
			}
		}
		&_active {
			.icon {
				&.icon-grid-list {
					.svg-icon-param('icon-grid-list', 18px, 18px, '[fill]: #1F9BD7');
				}
				&.icon-grid-block {
					.svg-icon-param('icon-grid-block', 18px, 18px, '[fill]: #1F9BD7');
				}
			}
		}
		&:hover {
			.icon {
				&.icon-grid-list {
					.svg-icon-param('icon-grid-list', 18px, 18px, '[fill]: #1F9BD7');
				}
				&.icon-grid-block {
					.svg-icon-param('icon-grid-block', 18px, 18px, '[fill]: #1F9BD7');
				}
			}
		}
	}
}

@media screen and (max-width: 980px) {
	.grid-view {
		display: none;
	}
}