.slider-descr {
	list-style-type: none;
	&__info {
		padding-left: 18px;
		position: relative;
		.regular();
		.font-size(18);
		color: #797979;
		line-height: 1.2;
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0;
		}
		&:after {
			content: '';
			width: 8px;
			height: 8px;
			background: #797979;
			position: absolute;
			top: 50%;
			margin-top: -4px;
			left: 0px;
			border-radius: 50%;
		}
	}
}

@media screen and (max-width: 980px) {
	.slider-descr {
		&__info {
			.font-size(16);
			margin-bottom: 10px;
		}
	}
}