.m-nav-catalog {
	display: none;
	&__section {
		&_active {
			.m-nav-catalog__trigger {
				&:after {
					transform: rotate(-90deg);
				}
				&:before {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}
	&__trigger {
		padding: 0 20px;
		background: #FBFBFB;
		height: 60px;
		line-height: 60px;
		position: relative;
		.regular();
		.font-size(16);
		color: #797979;
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			margin-top: -6px;
			right: 20px;
			transition: transform .2s ease;
			.svg-icon('icon-collapse', 12px, 12px);
		}
		&:before {
			content: '';
			width: 100%;
			height: 1px;
			background: #F3F3F3;
			position: absolute;
			left: 0;
			bottom: 0;
			opacity: 0;
			visibility: hidden;
			transition: visibility .2s ease, opacity .2s ease;
		}
	}
	&__list {}
	&__item {
		border-bottom: 1px solid #F7F7F7;
		&:last-child {
			border-bottom: 0;
		}
	}
	&__link {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		text-decoration: none;
		padding: 17px 20px;
		position: relative;
		color: #63C2EF;
		&:after {
			content: '';
			display: block;
			.svg-icon-param('icon-arrow-right', 7px, 12px, '[fill]: #1F9BD7');
			position: absolute;
			top: 50%;
			margin-top: -6px;
			right: 20px;
		}
	}
	&__link-name {
		.regular();
		.font-size(14);
		line-height: 1.2;
		color: #63C2EF;
		flex-grow: 20;
		user-select: none;
		margin-right: 20px;
	}
	&__count {
		background: #F7F7F7;
		border-radius: 50px;
		padding: 4px 10px;
		margin-right: 22px;
		.font-size(14);
		line-height: normal;
		text-align: center;
		color: #797979;
	}
}

@media screen and (max-width: 768px) {
	.m-nav-catalog {
		display: block;
	}
}