.product-count {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	flex-wrap: wrap;
	height: 30px;
	margin-right: 5px;
	&__btn {
		height: 100%;
		width: 28px;
		cursor: pointer;
		overflow: hidden;
		&_minus {}
		&_plus {}
		.icon {
			display: block;
			position: relative;
			top: 50%;
			margin-top: -6px;
			left: 50%;
			margin-left: -4px;
			&.icon-arrow-left {
				.svg-icon-param('icon-arrow-left', 8px, 12px, '[fill]: #1F9BD7');
			}
			&.icon-arrow-right {
				.svg-icon-param('icon-arrow-right', 8px, 12px, '[fill]: #1F9BD7');
			}
		}
	}
	&__input-cover {
		height: 100%;
	}
	&__input {
		appearance: none;
		border-radius: 0;
		border: 0;
		box-shadow: none;
		background: none;
		padding: 0;
		margin: 0;
		&::placeholder {
			color: #423D3D;
		}
		text-align: center;
		height: 100%;
		border: 1px solid #E5E5E5;
		border-radius: 5px;
		.regular();
		.font-size(15);
		color: #423D3D;
		width: 40px;
	}
}