.header-nav {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	flex-wrap: wrap;
	&_short {
		display: none;
		list-style-type: none;
		list-style-image: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7);
		.header-nav__item {
			margin-right: 34px;
			margin-bottom: 0;
		}
		.scroll-to-fixed-fixed & {
			display: flex;
		}
	}
	&__item {
		margin-right: 30px;
		margin-bottom: 3px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__link {
		color: #63C2EF;
		text-decoration: none;
		&:hover {
			.header-nav__link-name {
				text-decoration: none;
			}
		}
		&_region {
			.header-nav__link-name {
				text-decoration: none;
				border-bottom: 1px dashed #63C2EF;
			}
			&:hover {
				.header-nav__link-name {
					border-color: transparent;
				}
			}
		}
	}
	&__link-name {
		.regular();
		.font-size(13);
		line-height: 1.2;
		text-decoration: underline;
		color: #63C2EF;
		transition: border-color .2s ease;
	}

	.icon {
		display: inline-block;
		vertical-align: middle;
		margin-right: 3px;
		&.icon-car {
			.svg-icon('icon-car', 16px, 16px);
		}
		&.icon-gear {
			.svg-icon('icon-gear', 16px, 16px);
		}
		&.icon-mark {
			.svg-icon('icon-mark', 16px, 16px);
		}
		&.icon-building {
			.svg-icon('icon-building', 16px, 16px);
		}
	}
}

@media screen and (max-width: 980px) {
	.header-nav {
		&_short {
			margin-right: 20px;
			.header-nav__item {
				margin-right: 20px;
				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}