.mobile-nav {
	&_white {
		.mobile-nav__item {
			&:after {
				background: #F7F7F7;
			}
		}
		.mobile-nav__link {
			background: white;
			&:after {
				.svg-icon-param('icon-arrow-right', 6px, 10px, '[fill]: #1F9BD7');
			}
		}
		.mobile-nav__link-name {
			color: #63C2EF;
		}
	}
	&__list {}
	&__item {
		position: relative;
		&:after {
			content: '';
			width: calc(~"100% - 30px");
			height: 1px;
			background: #F3F3F3;
			position: absolute;
			bottom: 0;
			left: 15px;
		}
		&:last-child {
			&:after {
				display: none;
			}
		}
	}
	&__link {
		text-decoration: none;
		display: block;
		padding: 20px 25px;
		position: relative;
		&:after {
			content: '';
			display: block;
			.svg-icon-param('icon-arrow-right', 6px, 10px, '[fill]: #797979');
			position: absolute;
			top: 50%;
			margin-top: -5px;
			right: 20px;
		}
	}
	&__link-name {
		.regular();
		.font-size(16);
		line-height: 1.2;
		color: #797979;
		display: inline-block;
		vertical-align: middle;
	}

	.icon {
		display: inline-block;
		vertical-align: middle;
		margin-right: 10px;
		&.icon-mark {
			.svg-icon('icon-mark', 20px, 20px);
		}
		&.icon-car {
			.svg-icon('icon-car', 20px, 20px);
		}
		&.icon-gear {
			.svg-icon('icon-gear', 20px, 20px);
		}
		&.icon-building {
			.svg-icon('icon-building', 20px, 20px);
		}
		&.icon-user {
			.svg-icon('icon-user', 20px, 20px);
		}
		&.icon-stack {
			.svg-icon('icon-stack', 20px, 20px);
		}
		&.icon-exit {
			.svg-icon('icon-exit', 20px, 20px);
		}
	}
}