.card-grid {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	flex-wrap: wrap;
	&_view-3 {
		.product-card {
			width: calc(~"(100% - 20px * 2) / 3");
			margin-right: 20px;
			&:nth-child(3n) {
				margin-right: 0;
			}
		}
	}
	&_view-4 {
		.product-card {
			width: calc(~"(100% - 20px * 3) / 4");
			margin-right: 20px;
			&:nth-child(4n) {
				margin-right: 0;
			}
		}
	}
	&_multiline {
		.product-card {
			margin-bottom: 20px;
		}
	}

	&_view-rows {
		flex-direction: column;
		.product-card {
			width: 100%;
			margin: 0 0 20px 0;
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			align-items: flex-start;
			&.product-card_outofstock {
				.product-card__available_hidden {
					display: flex;
					text-align: right;
				}
				.available {
					flex-direction: column;
					align-items: flex-end;
					.font-size(15);
					margin-bottom: 0;
				}
				.available__name {
					color: #F32D2D;
					margin-right: 0;
					margin-bottom: 24px;
				}
				.available__link {
					display: inline-block;
					margin-bottom: 10px;
				}
			}
		}
		.product-card__body {
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			align-items: flex-start;
		}
		.product-card__body-col {
			&:first-child {
				flex-basis: 160px;
				max-width: 160px;
				padding-right: 20px;
			}
			&:last-child {
				flex-basis: 325px;
				max-width: 325px;
				padding: 0 10px;
			}
		}
		.product-card__image-wrap {
			height: 140px;
		}
		.product-card__name-wrap {
			text-align: left;
			padding-top: 0;
			padding-bottom: 0;
			margin-bottom: 20px;
		}
		.product-card__name {
			.font-size(16);
			height: 40px;
			min-height: 40px;
		}
		.product-card__info-wrap {
			justify-content: flex-start;
		}
		.rating {
			margin-right: 10px;
			height: 16px;
			background-size: 23px;
			width: 110px;
		}
		.rating__fill {
			background-size: 23px;
		}
		.product-card__reviews-link {
			.font-size(13);
		}
		.product-card__characteristics {
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			align-items: flex-start;
			flex-wrap: wrap;
			margin-bottom: 13px;
		}
		.characteristics__col {
			padding-right: 15px;
		}
		.characteristics__row {
			.font-size(14);
			margin-bottom: 5px;
			line-height: 1.2;
		}
		.price-box {
			display: none;
		}
		.price-box_hidden {
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: flex-end;
			min-height: 52px;
		}
		.price-box__price {
			margin: 0;
			.font-size(24);
			line-height: 1.2;
		}
		.price-box__old-price {
			margin: 0;
			.font-size(20);
			line-height: 1.2;
		}
		.product-card__available_hidden {
			display: flex;
			text-align: right;
		}
		.available {
			.font-size(13);
			line-height: 1.2;
			margin-bottom: 18px;
		}
		.available__name {
			color: #423D3D;
		}
		.product-card__footer {
			flex-grow: 20;
			padding-left: 10px;
		}
		.product-card__features-tools {
			display: none;
		}
		.product-card__features-tools_hidden {
			display: block;
		}
		.product-card__bottom {
			display: block;
		}
	}
}

@media screen and (max-width: 980px) {
	.card-grid {
		&_view-4 {
			.product-card {
				width: calc(~"(100% - 20px * 2) / 3");
				margin-bottom: 20px;
				&:nth-child(4n) {
					margin-right: 20px;
				}
				&:nth-child(3n) {
					margin-right: 0;
				}
			}
		}
		&_in-catalog-page {
			.product-card {
				width: calc(~"(100% - 20px) / 2");
				&:nth-child(4n) {
					margin-right: 0;
				}
				&:nth-child(3n) {
					margin-right: 20px;
				}
				&:nth-child(2n) {
					margin-right: 0;
				}
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.card-grid {
		&_hide-on-mobile {
			display: none;
		}
		&_view-4 {
			.product-card {
				width: calc(~"(100% - 20px) / 2");
				margin-bottom: 20px;
				&:nth-child(4n) {
					margin-right: 0;
				}
				&:nth-child(3n) {
					margin-right: 0;
				}
				&:nth-child(2n) {
					margin-right: 0;
				}
			}
		}
	}
}

@media screen and (max-width: 620px) {
	.card-grid {
		&_hide-on-mobile {
			display: none;
		}
		&_view-4 {
			.product-card {
				width: 100%;
				max-width: 500px;
				margin: 0 auto 20px;
				&:nth-child(4n) {
					margin-right: auto;
				}
				&:nth-child(3n) {
					margin-right: auto;
				}
				&:nth-child(2n) {
					margin-right: auto;
				}
			}
		}
		&_in-catalog-page {
			.product-card {
				width: 100%;
				max-width: 500px;
				margin: 0 auto;
				&:nth-child(4n) {
					margin-right: auto;
				}
				&:nth-child(3n) {
					margin-right: auto;
				}
				&:nth-child(2n) {
					margin-right: auto;
				}
			}
		}
	}
}

@media screen and (max-width: 400px) {
	.card-grid {
		&_border {
			.product-card {
				margin: 0;
				border-bottom: 1px solid #F7F7F7;
				&:last-child {
					border-bottom: 0;
				}
			}
		}
	}
}