.product-card {
	padding: 17px 20px;
	background: #FFFFFF;
	border: 1px solid transparent;
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0);
	border-radius: 5px;
	transition: box-shadow .2s ease, border .2s ease;
	min-width: 220px;
	position: relative;
	align-self: stretch;
	&:hover {
		border-color: #EFEFEF;
		box-shadow: 0px 0px 15px rgba(0, 0, 0, .05);
		.product-card__tool {
			opacity: 1;
			visibility: visible;
		}
	}
	&_outofstock {
		.product-card__footer {
			align-self: stretch;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
		}
		.price-box {
			margin-bottom: 16px;
		}
		.price-box__old-price {
			.font-size(15);
			text-decoration: none;
		}
		.product-card__available_hidden {
			display: block;
			text-align: center;
		}
		.available {
			.font-size(13);
			margin-bottom: 0;
		}
		.available__name {
			color: #F32D2D;
			margin-right: 0;
			margin-bottom: 10px;
		}
		.available__link {
			display: inline-block;
			margin-bottom: 16px;
		}
		.price-box_hidden {
			margin-bottom: 0;
		}
		.product-card__button-wrap {
			display: none;
		}
		.product-card__bottom {
			display: none;
		}
	}
	&__body {}
	&__body-col {}
	&__label {
		position: absolute;
		top: -7px;
		right: -10px;
		background: #F32D2D;
		border-radius: 5px;
		text-align: center;
		.regular();
		.font-size(14);
		text-align: center;
		width: 54px;
		height: 26px;
		line-height: 26px;
		color: #FFFFFF;
		user-select: none;
		pointer-events: none;
		z-index: 2;
	}
	&__image-wrap {
		height: 180px;
		position: relative;
	}
	&__tool {
		appearance: none;
		box-shadow: none;
		border: 0;
		border-radius: 0;
		background: white;
		padding: 0;
		margin: 0;
		position: absolute;
		cursor: pointer;
		z-index: 2;
		transition: opacity .2s ease, visibility .2s ease;
		opacity: 0;
		visibility: hidden;
	}
	&__zoom-view {
		padding: 8px;
		left: -8px;
		top: -8px;
	}
	&__quick-view {
		background: rgba(0, 0, 0, 0.9);
		border-radius: 4px;
		width: 40px;
		height: 40px;
		left: 50%;
		top: 50%;
		margin-top: -20px;
		margin-left: -20px;
	}
	&__img-link {
		text-decoration: none;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;
	}
	&__image {
		max-width: 100%;
		max-height: 100%;
		vertical-align: middle;
	}
	&__name-wrap {
		padding: 10px 0;
		text-align: center;
		margin-bottom: 10px;
	}
	&__name {
		.regular();
		.font-size(14);
		line-height: 1.4;
		color: #63C2EF;
		display: inline-block;
		min-height: 57px;
		height: 57px;
		overflow-y: hidden;
		vertical-align: middle;
		&:hover {
			text-decoration: none;
		}
	}
	&__info-wrap {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 20px;
		flex-wrap: wrap;
	}
	&__reviews {
		text-align: right;
	}
	&__reviews-link {
		display: inline-block;
		.regular();
		.font-size(11);
		line-height: normal;
		color: #797979;
		&:hover {
			text-decoration: none;
		}
	}
	&__characteristics {
		display: none;
	}
	&__available {
		display: none;
		&_hidden {
			display: none;
		}
	}
	&__footer {}
	&__button-wrap {
		margin-bottom: 15px;
		text-align: center;
	}
	&__features-tools {
		&_hidden {
			display: none;
		}
	}
	&__bottom {
		display: none;
		text-align: right;
	}
	&__fast-buy {
		.regular();
		.font-size(15);
		line-height: 1.2;
		color: #63C2EF;
		text-decoration: none;
		border-bottom: 1px dashed #63C2EF;
		transition: border-color .2s ease;
		&:hover {
			border-bottom-color: transparent;
		}
	}
	.btn {
		min-width: 0;
		width: 100%;
	}
	.icon {
		display: inline-block;
		vertical-align: middle;
		&.icon-zoom {
			.svg-icon('icon-zoom', 18px, 18px);
		}
		&.icon-eye {
			.svg-icon('icon-eye', 18px, 12px);
		}
	}
}

@media screen and (max-width: 768px) {
	.product-card {
		padding: 25px 15px;
		box-shadow: none;
		&:hover {
			box-shadow: none;
			border-color: transparent;
		}
		&_outofstock {
			.price-box {
				margin-bottom: 10px;
			}
			.product-card__available {
				.available__name {
					margin-bottom: 15px;
				}
				.available__link {
					display: none;
				}
			}
			.product-card__available_hidden {
				display: block;
				text-align: left;
				order: 2;
				width: 50%;
				.available__name {
					display: none;
				}
				.available__link {
					display: inline;
					margin-bottom: 0;
					.font-size(14);
				}
			}
			.product-card__footer {
				align-self: flex-start;
				flex-direction: row;
			}
		}
		&__body {
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			align-items: flex-start;
		}
		&__body-col {
			width: 50%;
			&:first-child {
				padding-right: 15px;
			}
		}
		&__label {
			top: -10px;
			right: -5px;
		}
		&__zoom-view {
			left: 0;
			top: 0;
		}
		&__name-wrap {
			text-align: left;
			margin-bottom: 0;
			padding-bottom: 3px;
		}
		&__name {
			.font-size(13);
		}
		&__info-wrap {
			margin-bottom: 10px;
		}
		&__characteristics {
			display: block;
		}
		&__available {
			display: flex;
			&_hidden {
				display: none;
			}
		}
		&__features-tools {
			order: 1;
			width: 50%;
			padding-right: 15px;
		}
		&__footer {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
		}
		&__button-wrap {
			order: 2;
			margin-bottom: 0;
			width: 50%;
			.btn {
				min-width: 0;
				width: 100%;
			}
		}
	}
}

@media screen and (max-width: 620px) {
	.product-card {
		&__tool {
			display: none;
		}
	}
}