.set {
	&__list {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: stretch;
		margin: 0 -10px;
	}
	&__item {
		width: 200px;
		min-height: 367px;
		background: #FFFFFF;
		border: 1px dashed #C4C4C4;
		border-radius: 5px;
		margin: 10px;
		position: relative;
		transition: background .2s ease;
		&:before {
			content: attr(data-placeholder);
			.regular();
			.font-size(16);
			line-height: 1.4;
			text-align: center;
			color: #C4C4C4;
			width: 85px;
			height: 70px;
			position: absolute;
			left: 50%;
			margin-left: -42.5px;
			top: 50%;
			margin-top: -35px;
			user-select: none;
		}
		&:after {
			content: '';
			display: block;
			.svg-icon('icon-plus', 12px , 12px);
			position: absolute;
			top: 50%;
			margin-top: -6px;
			right: -17px;
		}
		&:nth-child(4n) {
			&:after {
				display: none;
			}
		}
		&_empty {
			.btn {
				display: none;
			}
			.btn_gray {
				display: block;
			}
		}
		&_append {
			.btn {
				display: block;
			}
			.btn_gray {
				display: none;
			}
			&:after {
				display: none;
			}
		}
	}
	&__add {
		display: none;
		position: absolute;
		text-align: center;
		width: 180px;
		height: 72px;
		top: 50%;
		margin-top: -36px;
		left: 50%;
		margin-left: -90px;
	}
	&__add-text {
		.regular();
		.font-size(16);
		line-height: 1.4;
		color: #C4C4C4;
		margin-bottom: 6px;
	}
	&__add-icon {
		display: inline-block;
		border-radius: 50%;
		width: 44px;
		height: 44px;
		background: #1F9BD7;
		position: relative;
		transform: rotate(0);
		transition: transform .2s ease;
		&:after {
			content: '';
			width: 18px;
			height: 2px;
			background: white;
			position: absolute;
			top: 50%;
			margin-top: -1px;
			left: 50%;
			margin-left: -9px;
		}
		&:before {
			content: '';
			width: 2px;
			height: 18px;
			background: white;
			position: absolute;
			top: 50%;
			margin-top: -9px;
			left: 50%;
			margin-left: -1px;
		}
	}
	&__info {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
		margin: 10px -40px;
		padding: 20px 40px;
		background: #FBFBFB;
	}
	&__text-wrap {
		flex-grow: 20;
		margin-right: 20px;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
	}
	&__discount-wrap {
		flex-grow: 20;
	}
	&__text {
		.regular();
		.font-size(14);
		line-height: 1.2;
		color: #423D3D;
	}
	&__discount {
		.semi-bold();
	}
	&__price-wrap {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
		.set__text_old {
			color: #C4C4C4;
			margin-right: 20px;
		}
	}
	&__price {
		.font-size(20);
	}
	&__discount-price {
		.semi-bold();
		.font-size(20);
	}
	&__btn-wrap {
		.btn {
			padding: 9px 35px;
		}
	}
	&__delete {
		appearance: none;
		border-radius: 0;
		border: 0;
		box-shadow: none;
		background: none;
		padding: 0;
		margin: 0;

		display: none;
		padding: 10px;
		position: absolute;
		z-index: 2;
		right: 5px;
		bottom: 5px;
		.icon {
			display: block;
			transition: background .2s ease;
			&.icon-trash {
				.svg-icon('icon-trash', 16px, 16px);
			}
		}
		&:hover {
			.icon {
				&.icon-trash {
					.svg-icon-param('icon-trash', 16px, 16px, '[fill]: #1F9BD7');
				}
			}
		}
	}
	&__card {
		min-width: 0;
		width: 100%;
		height: 100%;
		box-shadow: none;
		border: 0;
		padding-top: 17px;
		&:hover {
			border-color: transparent;
			box-shadow: none;
		}
		&_default {
			.product-card__footer {
				display: none;
			}
		}
		.product-card__image-wrap {
			height: 160px;
		}
		.product-card__name-wrap {
			margin-bottom: 0;
		}
		.price-box {
			margin: 4px 0 20px;
		}
		.product-card__button-wrap {
			margin-bottom: 0;
		}


		.product-card__label {
			display: none;
		}
		.product-card__tool {
			display: none;
		}
		.product-card__info-wrap {
			display: none;
		}
		.product-card__characteristics {
			display: none;
		}
		.product-card__available {
			display: none;
		}
		.product-card__available_hidden {
			display: none;
		}
		.product-card__features-tools {
			display: none;
		}
		.product-card__features-tools_hidden {
			display: none;
		}
		.price-box_hidden {
			display: none;
		}
		.product-card__bottom {
			display: none;
		}
		.product-card__footer_hidden {
			display: none;
		}
	}
}

@media screen and (max-width: 980px) {
	.set {
		&__list {
			flex-direction: column;
			align-items: flex-start;
			margin: 0;
		}
		&__item {
			width: 100%;
			min-height: 170px;
			margin: 10px 0;
			&:after {
				top: auto;
				margin-top: auto;
				right: 50%;
				margin-right: -6px;
				bottom: -17px;
			}
		}
		&__info {
			justify-content: center;
			flex-direction: column;
		}
		&__text-wrap {
			flex-grow: 0;
			margin-right: 0;
			justify-content: center;
			flex-direction: column;
		}
		&__discount-wrap {
			text-align: center;
		}
		&__text {
			margin-bottom: 10px;
			.font-size(16);
		}
		&__price-wrap {
			justify-content: center;
			flex-direction: column;
			.set__text_old {
				margin-right: 0;
			}
		}
		&__price {
			.font-size(23);
		}
		&__discount-price {
			.font-size(23);
		}
		&__btn-wrap {
			.btn {
				padding: 9px 35px;
				min-width: 215px;
			}
		}
		&__card {
			width: 100%;
			height: 100%;
			padding: 20px;
			.product-card__body {
				display: flex;
				flex-direction: row;
				justify-content: flex-start;
				align-items: flex-start;
			}
			.product-card__image-wrap {
				height: 150px;
			}
			.product-card__body-col {
				&:first-child {
					flex-basis: 150px;
					max-width: 150px;
					flex-shrink: 0;
				}
				&:last-child {
					flex-grow: 20;
					padding-left: 15px;
				}
			}
			.product-card__name-wrap {
				padding: 0;
				margin-bottom: 10px;
			}
			.product-card__name {
				min-height: 40px;
				height: 40px;
				text-align: left;
			}
			.product-card__info-wrap {
				display: flex;
				margin-bottom: 14px;
			}
			.price-box {
				margin: 0;
				justify-content: flex-start;
			}
			.price-box__price {
				margin: 0 8px 0 0;
			}
			.price-box__old-price {
				margin: 0;
			}
			.product-card__footer {
				padding-top: 15px;
				.btn {
					max-width: 215px;
					margin: 0 auto;
				}
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.set {
		&__card {
			.product-card__image-wrap {
				height: 150px;
			}
			.product-card__body-col {
				&:first-child {
					padding-right: 0;
					width: auto;
				}
				&:last-child {
					flex-grow: 20;
					padding-left: 15px;
					width: auto;
				}
			}
			.product-card__name {
				min-height: 37px;
				height: 37px;
			}
			.product-card__footer {
				justify-content: center;
			}
			.product-card__button-wrap {
				width: 100%;
			}
		}
	}
}

@media screen and (max-width: 620px) {
	.set {
		display: flex;
		flex-direction: column;
		position: relative;
		&__list {
			margin: 0;
		}
		&__item {
			border-width: 0 0 1px 0;
			border-style: solid;
			border-color: #F7F7F7;
			margin: 0;
			border-radius: 0;
			&:after {
				right: auto;
				left: 20px;
				width: 34px;
				height: 34px;
				background-position: 50% 50%;
				bottom: -17px;
				border-radius: 50%;
				border: 1px solid #F7F7F7;
				background-color: white;
				z-index: 2;
			}
			&_empty {
				&:before {
					display: none;
				}
				.price-box {
					margin-bottom: 35px;
				}
				.product-card__footer {
					padding-top: 0;
					.btn {
						display: none;
					}
					.set__delete {
						display: block;
					}
				}
			}
			&_active {
				background: #EFEFEF;
				.set__add-icon {
					transform: rotate(135deg);
				}
			}
		}
		&__add {
			display: block;
		}
		&__info {
			margin: 0;
			order: 1;
			background: white;
		}
		&__text {
			margin-bottom: 14px;
			&_current {
				margin-right: 10px;
			}
		}
		&__discount-wrap {
			.set__descr {
				display: inline-block;
			}
		}
		&__price-wrap {
			flex-direction: row;
			align-items: baseline;
			.set__text_old {
				order: 1;
			}
		}
		&__discount-price {
			.font-size(30);
		}
		&__price {
			.font-size(26);
			text-decoration: line-through;
		}
		&__descr {
			display: none;
		}
		&__btn-wrap {
			width: 100%;
			.btn {
				display: block;
				width: 100%;
				min-width: 0;
				max-width: 325px;
				.font-size(16);
				padding: 11px 20px;
				margin: 0 auto;
			}
		}
		&__slider {
			margin-top: -22px;
			&:before {
				content: '';
				display: block;
				position: absolute;
				bottom: 100%;
				left: 50%;
				margin-left: -13px;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 0 13px 12px 13px;
				border-color: transparent transparent white transparent;
			}
		}
		&__card {
			.product-card__footer {
				.btn {
					max-width: 160px;
				}
			}
		}
	}
}

@media screen and (max-width: 400px) {
	.set {
		&__card {
			padding: 15px;
			.product-card__image-wrap {
				height: 130px;
			}
			.product-card__body-col {
				&:first-child {
					flex-basis: 130px;
					max-width: 130px;
					flex-shrink: 0;
				}
			}
		}
		&__delete {
			right: 3px;
			bottom: 3px;
		}
	}
}