.available {
	margin-bottom: 15px;
	flex-direction: row;
	flex-wrap: wrap;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	.regular();
	.font-size(12);
	line-height: 1.2;
	&__name {
		color: #797979;
		margin-right: 6px;
	}
	&__link {
		text-decoration: none;
		border-bottom: 1px dashed #63C2EF;
		transition: border-color .2s ease;
		color: #63C2EF;
		&:hover {
			border-bottom-color: transparent;
		}
	}
}