.filter {
	background: #FBFBFB;
	border: 1px solid #EFEFEF;
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.05);
	border-radius: 5px;
	margin-bottom: 40px;
	&_in-popup {
		margin-bottom: 0;
		border-radius: 0;
		box-shadow: none;
		border: 0;
		background: white;
		.filter__tabs-nav {
			margin: 0 15px;
		}
		.filter__tabs-nav-link {
			.font-size(14);
			height: 53px;
			line-height: 51px;
		}
		.filter__content-wrap {
			padding: 0;
		}
		.filter__section {
			margin-bottom: 0;
			padding: 25px 20px;
			border-bottom: 1px solid #F7F7F7;
			.faq {
				display: none;
			}
		}
		.filter__s-content {
			margin-top: 25px;
		}
		.filter__s-title-wrap {
			justify-content: flex-start;
			margin-bottom: 0;
		}
		.filter__s-title {
			.font-size(16);
			width: 100%;
			.icon {
				margin-left: 6px;
				&.icon-expand {
					.svg-icon-param('icon-expand', 12px, 8px, '[fill]: #423D3D');
				}
			}
		}
		.filter__field {
			margin-bottom: 15px;
		}
		.filter__info {
			display: none;
		}
		.filter__footer {
			padding: 15px 20px;
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			.btn {
				width: auto;
				.font-size(14);
				padding: 8px 20px;
				order: 1;
				margin: 10px;
			}
			.btn_gray {
				order: 0;
			}
		}
	}
	&__head {
		padding: 10px 15px 5px;
	}
	&__title {
		text-align: center;
		.regular();
		.font-size(15);
		line-height: 1.2;
		color: #423D3D;
	}
	&__body {}
	&__tabs {}
	&__tabs-nav {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		border-bottom: 2px solid #EFEFEF;
	}
	&__tabs-nav-item {
		width: 50%;
		&_active {
			.filter__tabs-nav-link {
				color: #1F9BD7;
				&:after {
					opacity: 1;
					visibility: visible;
				}
			}
		}
		&:hover {
			.filter__tabs-nav-link {
				color: #1F9BD7;
			}
		}
	}
	&__tabs-nav-link {
		text-align: center;
		height: 44px;
		display: block;
		text-decoration: none;
		.regular();
		.font-size(15);
		line-height: 44px;
		padding: 0 10px;
		color: #797979;
		position: relative;
		transition: color .2s ease;
		&:after {
			content: '';
			width: 100%;
			height: 4px;
			background: #1F9BD7;
			position: absolute;
			left: 0;
			bottom: -2px;
			opacity: 0;
			visibility: hidden;
			transition: opacity .2s ease, visibility .2s ease;
		}
	}
	&__content-wrap {
		padding: 20px 15px 10px;
	}
	&__content {
		display: none;
		&:first-child {
			display: block;
		}
	}
	&__section {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__s-title-wrap {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		margin-bottom: 15px;
	}
	&__s-title {
		.regular();
		.font-size(14);
		line-height: 1.2;
		color: #423D3D;
		user-select: none;
		cursor: pointer;
		.icon {
			display: inline-block;
			vertical-align: 1px;
			margin-left: 1px;
			&.icon-expand {
				transition: transform .2s ease;
				.svg-icon-param('icon-expand', 8px, 5px, '[fill]: #423D3D');
			}
		}
		&_slide-up {
			.icon {
				&.icon-expand {
					transform: rotate(-90deg);
				}
			}
		}
	}
	&__s-content {
		position: relative;
		&_columns {
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			align-items: flex-start;
		}
	}
	&__s-col {
		width: 50%;
	}
	&__select {
		width: 100%;
	}
	&__field {
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__footer {
		padding: 10px 15px 15px;
	}
	&__info {
		text-align: center;
		.regular();
		.font-size(13);
		line-height: 1.2;
		color: #797979;
		margin-bottom: 10px;
		&_dark {
			color: #423D3D;
		}
	}
	&__matched {
		width: 105px;
		height: 50px;
		padding: 10px 5px;
		z-index: 3;
		background: #FFFFFF;
		border: 1px solid #EFEFEF;
		box-sizing: border-box;
		box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.05);
		position: absolute;
		left: calc(~"100% + 10px");
		top: 50%;
		margin-top: -25px;
		border-radius: 5px;
		.regular();
		.font-size(12);
		line-height: 1.2;
		text-align: center;
		color: #797979;
		&:before {
			content: "";
			position: absolute;
			top: 50%;
			margin-top: -8px;
			left: -12px;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 8px 12px 8px 0;
			border-color: transparent white transparent transparent;
		}
	}
	&__matched-top {}
	&__matched-bottom {
		color: #423D3D;
	}
	.btn {
		display: block;
		width: 100%;
		.font-size(13);
		padding: 7px 10px;
	}
	.btn_gray {
		margin-top: 10px;
	}
}

@media screen and (max-width: 400px) {
	.filter {
		&_in-popup {
			.filter__footer {
				flex-direction: column;
				.btn {
					order: 1;
					width: 100%;
					margin: 10px 0;
				}
				.btn_gray {
					order: 2;
				}
			}
		}
	}
}