.features-tools {
	text-align: center;
	&_text {
		.features-tools__btn {
			width: auto;
			padding: 5px 10px;
			height: 37px;
		}
	}
	&__btn {
		appearance: none;
		box-shadow: none;
		border: 0;
		border-radius: 0;
		background: white;
		padding: 0;
		margin: 0;
		width: 40px;
		height: 30px;
		display: inline-block;
		text-align: center;
		cursor: pointer;
		vertical-align: top;
		.icon {
			display: inline-block;
			vertical-align: middle;
			transition: background .2s ease;
			&.icon-compare {
				.svg-icon('icon-add-compare', 27px, 27px);
			}
			&.icon-heart {
				.svg-icon-param('icon-heart', 20px, 20px, '[fill]: #C4C4C4');
			}
		}
		&:hover {
			.icon {
				&.icon-compare {
					.svg-icon-param('icon-add-compare', 27px, 27px, '[fill]: #1F9BD7');
				}
				&.icon-heart {
					.svg-icon-param('icon-heart', 20px, 20px, '[fill]: #F32D2D');
				}
			}
		}
		&_active {
			.icon {
				&.icon-compare {
					.svg-icon-param('icon-add-compare', 27px, 27px, '[fill]: #1F9BD7');
				}
				&.icon-heart {
					.svg-icon-param('icon-heart', 20px, 20px, '[fill]: #F32D2D');
				}
			}
		}
	}
	&__text {
		.regular();
		.font-size(12);
		line-height: normal;
		color: #C4C4C4;
		margin-left: 3px;
	}
}