.page-404 {
	padding: 100px 0 200px;
	text-align: center;
	&__head {}
	&__img-wrap {
		&:last-child {
			margin-bottom: 20px;
		}
	}
	&__img {
		vertical-align: middle;
		max-width: 320px;
	}
	&__logo {
		vertical-align: middle;
		max-width: 330px;
	}
	&__info {}
	&__title {
		.days();
		.font-size(30);
		line-height: 1.25;
		color: #1F9BD7;
		margin-bottom: 10px;
	}
	&__descr {
		.regular();
		line-height: 1.4;
		.font-size(18);
		color: #423D3D;
		margin: 0 auto 40px;
		max-width: 415px;
	}
	.btn {
		min-width: 260px;
	}
}

@media screen and (max-width: 768px) {
	.page-404 {
		padding: 50px 0 100px;
	}
}

@media screen and (max-width: 620px) {
	.page-404 {
		padding: 25px 0 50px;
		&__title {
			.font-size(25);
		}
		&__descr {
			margin: 0 auto 20px;
		}
	}
}

@media screen and (max-width: 400px) {
	.page-404 {
		&__img {
			max-width: 100%;
		}
		&__logo {
			max-width: 100%;
		}
		&__title {
			.font-size(25);
		}
	}
}