.category-sub-menu {
	position: absolute;
	top: 100%;
	left: 0;
	width: 500%;
	z-index: 5;
	background: white;
	padding: 20px 45px 25px 50px;
	display: none;
	&:after {
		content: '';
		width: 100px;
	}
	&__inner-wrap {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: flex-start;
	}
	&__nav {
		flex-grow: 20;
	}
	&__nav-body {
		display: flex;
		flex-direction: row;
		align-items: flex-start;
		justify-content: flex-start;
		margin-bottom: 20px;
	}
	&__nav-footer {}
	&__col {
		margin-right: 35px;
		max-width: calc(~"(100% - 35px * 3) / 3");
	}
	&__title {
		.regular();
		.font-size(15);
		line-height: 1.2;
		color: #423D3D;
		margin-bottom: 20px;
	}
	&__show-more {
		text-decoration: none;
		color: #797979;
		&:hover {
			.category-sub-menu__show-more-text {
				text-decoration: none;
			}
		}
		.icon {
			display: inline-block;
			vertical-align: middle;
			&.icon-forward {
				.svg-icon('icon-forward', 9px, 8px);
			}
		}
	}
	&__show-more-text {
		.regular();
		.font-size(15);
		color: #797979;
		line-height: 1.2;
		text-decoration: underline;
	}
	&__car {
		flex-basis: 260px;
		flex-shrink: 0;
	}
	&__img {
		vertical-align: middle;
		max-width: 100%;
		margin-bottom: 7px;
	}
	.btn {
		width: 100%;
	}
}

@media screen and (max-width: 980px) {
	.category-sub-menu {
		padding: 20px 30px;
		&__inner-wrap {
			flex-direction: column;
		}
		&__nav {
			width: 100%;
		}
		&__col {
			max-width: calc(~"(100% - 20px * 2) / 3");
			margin-right: 20px;
			&:last-child {
				margin-right: 0;
			}
		}
		&__nav-footer {
			margin-bottom: 20px;
		}
		&__car {
			flex-basis: auto;
			flex-shrink: 1;
			max-width: 300px;
		}
		&__img {
			max-width: 200px;
		}
	}
}