.about-us {
	&__columns {
		width: 100%;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: flex-start;
	}
	&__col {
		&_descr {
			flex-grow: 20;
			padding-right: 30px;
		}
		&_map {
			flex-basis: 380px;
			flex-shrink: 0;
		}
	}
	&__title {
		.days();
		.font-size(24);
		line-height: normal;
		color: #423D3D;
		margin-bottom: 25px;
	}
	&__info {
		.regular();
		.font-size(14);
		line-height: 1.4;
		color: #797979;
		margin-bottom: 30px;
	}
	&__map {
		box-shadow: 0px 0px 50px rgba(31, 155, 215, 0);
		border-radius: 5px;
		background: #F8F8F8;
		width: 100%;
		height: 240px;
		transition: box-shadow .2s ease;
		&:hover {
			box-shadow: 0px 0px 50px rgba(31, 155, 215, 0.1);
		}
	}
	&__footer {
		text-align: center;
		display: none;
	}
}

@media screen and (max-width: 768px) {
	.about-us {
		&__columns {
			flex-direction: column;
		}
		&__col {
			width: 100%;
			&_descr {
				flex-grow: 0;
				padding-right: 0;
				.btn {display: none;}
			}
			&_map {
				flex-basis: auto;
				margin-bottom: 25px;
			}
		}
		&__footer {
			display: block;
		}
	}
}

@media screen and (max-width: 400px) {
	.about-us {
		&__title {
			.font-size(18);
		}
		&__info {
			margin-bottom: 25px;
		}
	}
}