.catalog-page {
	&__inner-wrap {
		padding-top: 20px;
		padding-bottom: 40px;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
	}
	&__col {
		&_filter {
			flex-basis: 220px;
			max-width: 220px;
			flex-shrink: 0;
			margin-right: 20px;
		}
		&_content {
			flex-grow: 20;
			padding-bottom: 30px;
		}
	}
	&__head {
		padding-bottom: 20px;
	}
	&__title {
		.days();
		.font-size(24);
		line-height: 1.5;
		color: #423D3D;
		margin-bottom: 10px;
	}
	&__body {
		padding-top: 20px;
	}
	&__products {
		margin: 0;
		padding: 0;
		.product-set__title {
			display: block;
			text-align: center;
			margin-bottom: 20px;
			line-height: normal;
			font-size: medium;
		}
		.product-set__wrap {
			flex-wrap: wrap;
			flex-direction: column;
		}
		.product-set__item {
			margin-bottom: 15px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&__footer {}
	&__bottom {
		text-align: center;
		margin-bottom: 20px;
		.btn {
			min-width: 250px;
			&:last-child {
				display: none;
			}
		}
	}
	&__per-page {
		position: absolute;
		right: 0;
		top: 6px;
	}
}

@media screen and (max-width: 980px) {
	.catalog-page {
		&__products {
			.product-set__item {
				width: 100%;
				margin-right: 0;
			}
			.product-card__body-col {
				&:first-child {
					width: auto;
				}
				&:last-child {
					width: auto;
				}
			}
			.product-card__image-wrap {
				height: 50px;
			}
			.product-card__info-wrap {
				display: none;
			}
			.product-card__name {
				.font-size(12);
				min-height: 34px;
				height: 34px;
			}
		}
		&__per-page {
			position: relative;
			right: auto;
			top: auto;
			width: 170px;
			margin: 20px auto 0;
		}
	}
}

@media screen and (max-width: 768px) {
	.catalog-page {
		&__inner-wrap {
			flex-direction: column;
			padding-top: 0;
		}
		&__col {
			&_filter {
				display: none;
			}
			&_content {
				flex-grow: 0;
				width: 100%;
				padding-bottom: 0;
			}
		}
		&__head {
			padding: 0;
		}
		&__title {
			margin-bottom: 0;
			padding: 25px 20px;
		}
		&__body {
			padding-top: 0;
		}
		&__bottom {
			margin-bottom: 0;
			.btn {
				min-width: 180px;
				display: none;
				&:last-child {
					display: inline-block;
				}
			}
		}
	}
}

@media screen and (max-width: 400px) {
	.catalog-page {
		&__title {
			.font-size(18);
		}
		&__bottom {
			padding-top: 25px;
		}
	}
}